import { 
  SELECT_TYPE_SUCCESS,
  SELECT_TYPE_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_TYPE_SUCCESS,
  UPDATE_TYPE_ERROR,
  CANCEL_TYPE_INIT,
  TYPE_LIST,
  TYPE_HEADERS,
  GET_TYPES_SUCCESS,
  DELETE_TYPE_SUCCESS,
  DELETE_TYPE_ERROR,
  ORDER_TYPE_SUCCESS,
  FILTER_TYPE_SUCCESS
} from './constants';
  
  export const initialState = {
    types: TYPE_LIST,
    totalTypes: [],
    typeHeaders: TYPE_HEADERS,
    selectedType: false, 
    deleteSuccess: false,
    formType: {}
  };
  
  const reducer = (state = initialState, action) => {
    const newState = { ...state };
    const { payload } = action;
    const { types } = payload ? payload : { types: false };

    switch (action.type) {
      case GET_TYPES_SUCCESS:
          newState.types = types;
          newState.totalTypes = types;
          
          return {
            ...newState
          };
      case SELECT_TYPE_INIT:    
        return {
          ...newState
        };

      case SELECT_TYPE_SUCCESS:  
        newState.selectedType = payload;   
        newState.deleteSuccess = false;  
        newState.formType = payload; 
        return {
          ...newState
        };
        
      case DELETE_TYPE_SUCCESS: 
        newState.selectedType = false; 
        newState.deleteSuccess = payload; 
        return {
          ...newState
        };

      case UPDATE_FIELD_ATTRIBUTE:
        const { value: { fieldName, fieldValue } } = action;
        let newFormObject = {...newState.selectedType};
        newFormObject[fieldName] = fieldValue;
        newState.deleteSuccess = false;
        newState.formType = newFormObject;
        newState.selectedType = newFormObject;

        return {
          ...newState
        };
      case UPDATE_TYPE_SUCCESS:
        newState.deleteSuccess = payload;
        newState.formType = {};
        newState.selectedType = false;

        return {
          ...newState
        };

        case UPDATE_TYPE_ERROR:
          newState.deleteSuccess = payload;
          newState.formType = {};
          newState.selectedType = false;
  
          return {
            ...newState
          };
        
        case DELETE_TYPE_ERROR:
          newState.deleteSuccess = payload;
          newState.formType = {};
          newState.selectedType = false;
    
          return {
            ...newState
          };  

        case CANCEL_TYPE_INIT:
          newState.deleteSuccess = false;
          newState.formType = {};
          newState.selectedType = false;
  
          return {
            ...newState
          };
        case ORDER_TYPE_SUCCESS:
            newState.deleteSuccess = false;
            newState.formType = {};
            newState.selectedType = false;
            newState.types = types;
            return {
              ...newState
            };
        case FILTER_TYPE_SUCCESS:
              newState.deleteSuccess = false;
              newState.formType = {};
              newState.selectedType = false;
              newState.types = types;
              return {
                ...newState
              };            
      default:
        return {
          ...newState
        };
    }
  };
  
  export default reducer;
  