import {
    UPDATE_FIELD_ATTRIBUTE,
    UPDATE_WORKER_INIT,
    UPDATE_WORKER_SUCCESS,
    UPDATE_WORKER_ERROR,
    GET_WORKERS_SUCCESS,
    GET_WORKERS_ERROR,
    SELECT_WORKER_INIT,
    SELECT_WORKER_SUCCESS,
    SELECT_WORKER_ERROR,
    DELETE_WORKER_INIT,
    DELETE_WORKER_SUCCESS,
    DELETE_WORKER_ERROR,
    FILTER_WORKER_INIT,
    FILTER_WORKER_SUCCESS,
    FILTER_WORKER_ERROR,
    ORDER_WORKER_ERROR,
    ORDER_WORKER_SUCCESS,
    ORDER_WORKER_INIT
} from './constants'

export function updateWorker (payload) {
    return {
        type: UPDATE_WORKER_INIT,
        payload
    };
}

export function updateWorkerSuccess (payload) {
    return {
        type: UPDATE_WORKER_SUCCESS,
        payload
    };
}

export function updateWorkerError (payload) {
    return {
        type: UPDATE_WORKER_ERROR,
        payload
    };
}

export function updateWorkers (payload) {
    return {
        type: UPDATE_FIELD_ATTRIBUTE,
        payload
    };
}

export function selectWorker(payload) {
    return {
        type: SELECT_WORKER_INIT,
        payload
    };
}

export function selectWorkerSuccess(payload) {
    return {
        type: SELECT_WORKER_SUCCESS,
        payload,
    };
}

export function selectWorkerError(payload) {
    return {
        type: SELECT_WORKER_ERROR,
        payload,
    };
}

