import React, { Component } from "react";
import "./style.css";
import { connect } from "react-redux";
import CustomForm from "../../components/customform/customform";
import CustomTable from "../../components/table/table";
import { getTypes, getSelectedType, getDeleteType, getFormType, getTotalTypes } from "./selectors";
import { Spinner, Alert } from 'react-bootstrap';
import { 
  getTypeActions,
  getTypeButton,
  GET_TYPES,
  SELECT_TYPE_INIT, 
  TYPE_FORMS, 
  DELETE_TYPE_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_TYPE_INIT,
  CANCEL_TYPE_INIT,
  ORDER_TYPE_INIT,
  FILTER_TYPE_INIT,
} from './constants'


class ComponentType extends Component {

  componentWillMount () {
    const { getTypes } = this.props;
    getTypes();
  }

  componentWillReceiveProps (nextProps) {
    const { getTypes } = nextProps;

    if (nextProps.deleteType && this.props.deleteType !== nextProps.deleteType) {
      getTypes();
    }
  }

  render() {
    const {
      typeHeaders, 
      types,
      selectType,
      selectedType,
      removeType,
      deleteType,
      updateAttributes,
      updatedType,
      updateType,
      cancelType,
      orderType,
      filterType,
      totalTypes
  } = this.props;

    return (
      <div className="App">
        { deleteType ?
          <Alert  variant="success">
            {deleteType.message}
          </Alert>
          : ''
        }

        {!selectedType ? 
          <div className='type-list_wrapper'>
          { types && !types.emprty ? 
            <CustomTable 
              onSelectRow={selectType} 
              className="types-table" 
              items={types}
              totalItems={totalTypes}
              orderTable={orderType}
              headers={typeHeaders}
              filterTable = {filterType}
              paginator = {true}
              buttons={getTypeButton(() => selectType({}))} 
            /> :
            <Spinner className="spinner-wrapper" animation="border" role="status">
              <span className="sr-only">Loading...</span>
            </Spinner>
          }
          
        </div> :  
        <div className='type-form_wrapper'>
          <CustomForm 
            formName="customType" 
            values={ selectedType } 
            items={ TYPE_FORMS } 
            updateAction = { updateAttributes }
            buttons={
              getTypeActions(
                () => updateType(updatedType), 
                () => cancelType(), 
                () => removeType(selectedType)
            )} 
            />
        </div>
        }
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    typeHeaders: state.typeHeaders,
    types: getTypes(state),
    selectedType: getSelectedType(state),
    deleteType: getDeleteType(state),
    updatedType: getFormType(state),
    totalTypes: getTotalTypes(state),
  };
};

const mapDispachToProps = dispatch => {
  return { 
    getTypes: () => dispatch({ type: GET_TYPES, value: 1 }),
    updateAttributes: (value) => dispatch({ type: UPDATE_FIELD_ATTRIBUTE, value }),
    selectType: (typeValue) => dispatch({ type: SELECT_TYPE_INIT, value: typeValue }),
    removeType: (typeValue) => dispatch({ type: DELETE_TYPE_INIT, value: typeValue }),
    updateType: (typeValue) => dispatch({ type: UPDATE_TYPE_INIT, value: typeValue }),
    cancelType: () => dispatch({ type: CANCEL_TYPE_INIT}),
    orderType: (prop) => dispatch({ type: ORDER_TYPE_INIT, value: prop }),
    filterType: (prop) => dispatch({ type: FILTER_TYPE_INIT, value: prop }),
  };
};

export default connect(
  mapStateToProps,
  mapDispachToProps
)(ComponentType);