import { faUserCircle } from "@fortawesome/free-solid-svg-icons";
export const FILLER_TEXT = `But I must `;

export const getComponentItems = (userPicture) => {
    return [
        {
            path: '/',
            icon: faUserCircle,
            key: 1,
            description: 'Datos del usuario',
            picture: userPicture
        }
    ]
}