import { 
  STATE_AGE_UP_ASYNC, 
  STATE_AGE_DOWN, 
  SELECT_SECTOR_SUCCESS,
  SELECT_SECTOR_INIT,
  GET_USER_SUCCESS, 
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_SECTOR_SUCCESS,
  CANCEL_SECTOR_INIT,
  USER_LIST,
  USER_HEADERS,
  GET_SECTORS_SUCCESS,
  DELETE_SECTOR_SUCCESS,
  ORDER_SECTOR_SUCCESS,
  FILTER_SECTOR_SUCCESS,
} from './constants';
  
  export const initialState = {
    age: 40,
    email: 'jvera@gmail.com',
    users: USER_LIST,
    userHeaders: USER_HEADERS,
    selectedSector: false, 
    deleteSuccess: false,
    formSector: {},
    totalSectors: [],
  };
  
  const reducer = (state = initialState, action) => {
    const newState = { ...state };
    const { payload } = action;
    const { sectors } = payload ? payload : { sectors: false };

    switch (action.type) {
      case STATE_AGE_UP_ASYNC:
        newState.age += action.value;
        newState.deleteSuccess = false; 
        return {
          ...newState
        };

      case STATE_AGE_DOWN:
        newState.age -= action.value;
        newState.deleteSuccess = false; 
        return {
          ...newState
        };

      case GET_USER_SUCCESS:
            const { payload: { data: {email, id} } } = action;
            newState.email = email;
            newState.id = id;

            return {
              ...newState
            };
      case GET_SECTORS_SUCCESS:
          newState.sectors = sectors;
          newState.totalSectors = sectors;

          return {
            ...newState
          };
      case SELECT_SECTOR_INIT:    
        return {
          ...newState
        };

      case SELECT_SECTOR_SUCCESS:  
        newState.selectedSector = payload;   
        newState.deleteSuccess = false;  
        newState.formSector = payload; 
        return {
          ...newState
        };
        
      case DELETE_SECTOR_SUCCESS: 
        newState.selectedSector = false; 
        newState.deleteSuccess = payload; 
        return {
          ...newState
        };

      case UPDATE_FIELD_ATTRIBUTE:
        const { value: { fieldName, fieldValue } } = action;
        let newFormObject = {...newState.selectedSector};
        newFormObject[fieldName] = fieldValue;
        newState.deleteSuccess = false;
        newState.formSector = newFormObject;
        newState.selectedSector = newFormObject;

        return {
          ...newState
        };
      case UPDATE_SECTOR_SUCCESS:
        newState.deleteSuccess = payload;
        newState.formSector = {};
        newState.selectedSector = false;

        return {
          ...newState
        };
      case CANCEL_SECTOR_INIT:
          newState.deleteSuccess = false;
          newState.formSector = {};
          newState.selectedSector = false;
  
          return {
            ...newState
          };
      case ORDER_SECTOR_SUCCESS:
            newState.deleteSuccess = false;
            newState.formSector = {};
            newState.selectedSector = false;
            newState.sectors = sectors;
            return {
              ...newState
            };
      case FILTER_SECTOR_SUCCESS:
              newState.deleteSuccess = false;
              newState.formSector = {};
              newState.selectedSector = false;
              newState.sectors = sectors;
              return {
                ...newState
              };     
      default:
        return {
          ...newState
        };
    }
  };
  
  export default reducer;
  