import { takeLatest, put, call } from "redux-saga/effects";
import { GET_LOCATIONS_API, PATH_API } from '../../Constants';
import { 
  GET_LOCATIONS,
  SELECT_LOCATION_INIT,
  DELETE_LOCATION_INIT,
  UPDATE_LOCATION_INIT,
  ORDER_LOCATION_INIT, 
  FILTER_LOCATION_INIT
} from './constants';
import { 
  selectLocationSuccess,
  getLocationsSuccess,
  getLocationsError, 
  deleteLocationSuccess,
  deleteLocationError,
  updateLocationSuccess,
  updateLocationError,
  orderLocationsSuccess,
  filterLocationsSuccess
} from './actions';

function* selectLocation(action) {
  const { value } = action;

  yield put(selectLocationSuccess(value));
  return;
}

function* getLocations() {
  const urlLocations = `${ PATH_API }${GET_LOCATIONS_API}`;

  const response = yield call(fetch, urlLocations, {
      method: 'GET',
  });

  if (response.status >= 200 && response.status < 300) {
    const successResponse = yield call([response, response.json]);

    yield put(getLocationsSuccess(successResponse));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(getLocationsError(errorResponse));
}

function* removeLocation(action) {
  const { value } = action;
  const { id } = value ? value : {};

  const urlLocations = `${ PATH_API }${GET_LOCATIONS_API}/${id}`;

  const response = yield call(fetch, urlLocations, {
      method: 'DELETE',
  });

  if (response.status >= 200 && response.status < 300) {
    value.message = "Direccin eliminada exitosamente"; 
    yield put(deleteLocationSuccess(value));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(deleteLocationError(errorResponse));
}

function* updateLocation(action) {
  const { value } = action;
  const { id } = value ? value : {};
  
  const locationToUpdate = { ...value };

  value.message = "Direccin creada exitosamente"; 
  let locationId = id ? id : "0";
  let urlLocations = `${ PATH_API }${GET_LOCATIONS_API}`;

  const getResponse = yield call(fetch, `${urlLocations}/${locationId}`, {
    method: 'GET'
  });

  const callBody = {
    method: 'POST',
    body: JSON.stringify(locationToUpdate)
  }

  if (getResponse.status >= 200 && getResponse.status < 300) {
    value.message = "Direccin actualizada exitosamente"; 
    callBody.method = 'PUT';
    urlLocations = `${urlLocations}/${id}`
  }

  const response = yield call(fetch, urlLocations, {
      ...callBody
  });

  if (response.status >= 200 && response.status < 300) {
    yield put(updateLocationSuccess(value));
    return;
  }
  
  const errorResponse = yield call([response, response.json]);
  value.message = "Hubo un problema creando los cambios"; 
  yield put(updateLocationError(errorResponse));
}

function* orderLocation(action) { 
  const { value: {column, items, sense} } = action;

  items.sort((a,b) => {
    if(a[column] < b[column]) {
      return sense;
    }
    if(a[column] > b[column]) {
      return -1*sense;
    }
    return 0;
  })

  const response = {
    locations: items 
  }

  yield put(orderLocationsSuccess(response));
}

function* filterLocation(action) { 
  const { value: {fieldValue, items} } = action;
  
  let filtered = items.filter(item => {
    let itemFiltered = false;

    Object.keys(item).map(key => {
      const keyToFilter = item[key] && typeof item[key] !== 'object' ? item[key].toString().toLowerCase() : ""; 
      itemFiltered = itemFiltered || keyToFilter.includes(fieldValue);
      return true;
    })

    return itemFiltered;
  });

  const response = {
    locations: filtered
  }

  yield put(filterLocationsSuccess(response));
}

export function* watchLocation() {
  yield takeLatest(GET_LOCATIONS, getLocations);
  yield takeLatest(SELECT_LOCATION_INIT, selectLocation);
  yield takeLatest(DELETE_LOCATION_INIT, removeLocation);
  yield takeLatest(UPDATE_LOCATION_INIT, updateLocation);
  yield takeLatest(ORDER_LOCATION_INIT, orderLocation);
  yield takeLatest(FILTER_LOCATION_INIT, filterLocation);
}
