import { takeLatest, put, call } from "redux-saga/effects";
import { GET_TYPES_API, PATH_API, GET_SECTORS_API } from '../../Constants';
import { 
  GET_TYPES,
  SELECT_TYPE_INIT,
  DELETE_TYPE_INIT,
  UPDATE_TYPE_INIT,
  ORDER_TYPE_INIT, 
  FILTER_TYPE_INIT
} from './constants';
import { 
  selectTypeSuccess,
  getTypesSuccess,
  getTypesError, 
  deleteTypeSuccess,
  deleteTypeError,
  updateTypeSuccess,
  updateTypeError,
  orderTypesSuccess,
  filterTypesSuccess
} from './actions';

function* selectType(action) {
  const { value } = action;

  yield put(selectTypeSuccess(value));
  return;
}

function* getTypes() {
  const urlTypes = `${ PATH_API }${GET_TYPES_API}`;

  const response = yield call(fetch, urlTypes, {
      method: 'GET',
  });

  if (response.status >= 200 && response.status < 300) {
    const successResponse = yield call([response, response.json]);

    yield put(getTypesSuccess(successResponse));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(getTypesError(errorResponse));
}

function* removeType(action) {
  const { value } = action;
  const { id } = value ? value : {};

  const urlTypes = `${ PATH_API }${GET_TYPES_API}/${id}`;

  const response = yield call(fetch, urlTypes, {
      method: 'DELETE',
  });

  if (response.status >= 200 && response.status < 300) {
    value.message = "Direccin eliminada exitosamente"; 
    yield put(deleteTypeSuccess(value));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(deleteTypeError(errorResponse));
}

function* updateType(action) {
  const { value } = action;
  const { id } = value ? value : {};
  
  const typeToUpdate = { ...value };

  value.message = "Direccin creada exitosamente"; 
  let typeId = id ? id : "0";
  let urlTypes = `${ PATH_API }${GET_TYPES_API}`;

  const getResponse = yield call(fetch, `${urlTypes}/${typeId}`, {
    method: 'GET'
  });

  const callBody = {
    method: 'POST',
    body: JSON.stringify(typeToUpdate)
  }

  if (getResponse.status >= 200 && getResponse.status < 300) {
    value.message = "Direccin actualizada exitosamente"; 
    callBody.method = 'PUT';
    urlTypes = `${urlTypes}/${id}`
  }

  const response = yield call(fetch, urlTypes, {
      ...callBody
  });

  if (response.status >= 200 && response.status < 300) {
    yield put(updateTypeSuccess(value));
    return;
  }
  
  const errorResponse = yield call([response, response.json]);
  value.message = "Hubo un problema creando los cambios"; 
  yield put(updateTypeError(errorResponse));
}

function* orderType(action) { 
  const { value: {column, items, sense} } = action;

  items.sort((a,b) => {
    if(a[column] < b[column]) {
      return sense;
    }
    if(a[column] > b[column]) {
      return -1*sense;
    }
    return 0;
  })

  const response = {
    types: items 
  }

  yield put(orderTypesSuccess(response));
}

function* filterType(action) { 
  const { value: {fieldValue, items} } = action;
  
  let filtered = items.filter(item => {
    let itemFiltered = false;

    Object.keys(item).map(key => {
      const keyToFilter = item[key] && typeof item[key] !== 'object' ? item[key].toString().toLowerCase() : ""; 
      itemFiltered = itemFiltered || keyToFilter.includes(fieldValue);
      return true;
    })

    return itemFiltered;
  });

  const response = {
    types: filtered
  }

  yield put(filterTypesSuccess(response));
}

export function* watchType() {
  yield takeLatest(GET_TYPES, getTypes);
  yield takeLatest(SELECT_TYPE_INIT, selectType);
  yield takeLatest(DELETE_TYPE_INIT, removeType);
  yield takeLatest(UPDATE_TYPE_INIT, updateType);
  yield takeLatest(ORDER_TYPE_INIT, orderType);
  yield takeLatest(FILTER_TYPE_INIT, filterType);
}
