import {
    GET_ZONE_INIT,
    GET_ZONE_SUCCESS,
    GET_ZONE_ERROR
} from './constants'

export function getZones (payload) {
    return {
        type: GET_ZONE_INIT,
        payload
    };
}

export function getZoneSuccess (payload) {
    return {
        type: GET_ZONE_SUCCESS,
        payload
    };
}

export function getZoneError (payload) {
    return {
        type: GET_ZONE_ERROR,
        payload
    };
}