import { 
  SELECT_WORKER_SUCCESS,
  SELECT_WORKER_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_WORKER_SUCCESS,
  UPDATE_WORKER_ERROR,
  CANCEL_WORKER_INIT,
  WORKER_LIST,
  WORKER_HEADERS,
  GET_WORKERS_SUCCESS,
  DELETE_WORKER_SUCCESS,
  DELETE_WORKER_ERROR,
  ORDER_WORKER_SUCCESS,
  FILTER_WORKER_SUCCESS
} from './constants';
  
  export const initialState = {
    workers: WORKER_LIST,
    totalWorkers: [],
    workerHeaders: WORKER_HEADERS,
    selectedWorker: false, 
    deleteSuccess: false,
    formWorker: {}
  };
  
  const reducer = (state = initialState, action) => {
    const newState = { ...state };
    const { payload } = action;
    const { workers } = payload ? payload : { workers: false };

    switch (action.type) {
      case GET_WORKERS_SUCCESS:
          newState.workers = workers;
          newState.totalWorkers = workers;
          
          return {
            ...newState
          };
      case SELECT_WORKER_INIT:    
        return {
          ...newState
        };

      case SELECT_WORKER_SUCCESS:  
        newState.selectedWorker = payload;   
        newState.deleteSuccess = false;  
        newState.formWorker = payload; 
        return {
          ...newState
        };
        
      case DELETE_WORKER_SUCCESS: 
        newState.selectedWorker = false; 
        newState.deleteSuccess = payload; 
        return {
          ...newState
        };

      case UPDATE_FIELD_ATTRIBUTE:
        const { value: { fieldName, fieldValue } } = action;
        let newFormObject = {...newState.selectedWorker};
        newFormObject[fieldName] = fieldValue;
        newState.deleteSuccess = false;
        newState.formWorker = newFormObject;
        newState.selectedWorker = newFormObject;

        return {
          ...newState
        };
      case UPDATE_WORKER_SUCCESS:
        newState.deleteSuccess = payload;
        newState.formWorker = {};
        newState.selectedWorker = false;

        return {
          ...newState
        };

        case UPDATE_WORKER_ERROR:
          newState.deleteSuccess = payload;
          newState.formWorker = {};
          newState.selectedWorker = false;
  
          return {
            ...newState
          };
        
        case DELETE_WORKER_ERROR:
          newState.deleteSuccess = payload;
          newState.formWorker = {};
          newState.selectedWorker = false;
    
          return {
            ...newState
          };  

        case CANCEL_WORKER_INIT:
          newState.deleteSuccess = false;
          newState.formWorker = {};
          newState.selectedWorker = false;
  
          return {
            ...newState
          };
        case ORDER_WORKER_SUCCESS:
            newState.deleteSuccess = false;
            newState.formWorker = {};
            newState.selectedWorker = false;
            newState.workers = workers;
            return {
              ...newState
            };
        case FILTER_WORKER_SUCCESS:
              newState.deleteSuccess = false;
              newState.formWorker = {};
              newState.selectedWorker = false;
              newState.workers = workers;
              return {
                ...newState
              };            
      default:
        return {
          ...newState
        };
    }
  };
  
  export default reducer;
  