import React, { Component } from "react";
import "./style.css";
import { connect } from "react-redux";
import CustomForm from "../../components/customform/customform";
import CustomTable from "../../components/table/table";
import { getZones, getSelectedZone, getDeleteZone, getFormZone, getTotalZones } from "./selectors";
import { Spinner, Alert } from 'react-bootstrap';
import { 
  getZoneActions,
  getZoneButton,
  GET_ZONES,
  SELECT_ZONE_INIT, 
  ZONE_FORMS, 
  DELETE_ZONE_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_ZONE_INIT,
  CANCEL_ZONE_INIT,
  FILTER_ZONE_INIT,
} from './constants'


class ComponentZone extends Component {

  componentWillMount () {
    const { getZones } = this.props;
    getZones();
  }

  componentWillReceiveProps (nextProps) {
    const { getZones } = nextProps;

    if (nextProps.deleteZone && this.props.deleteZone !== nextProps.deleteZone) {
      getZones();
    }
  }

  render() {
    const {
      zoneHeaders, 
      zones,
      selectZone,
      selectedZone,
      removeZone,
      deleteZone,
      updateAttributes,
      updatedZone,
      updateZone,
      cancelZone,
      totalZones,
      filterZones,
  } = this.props;

    return (
      <div className="App">
        { deleteZone ?
          <Alert  variant="success">
            {deleteZone.message}
          </Alert>
          : ''
        }

        {!selectedZone ? 
          <div className='zone-list_wrapper'>
          { zones && !zones.emprty ? 
            <CustomTable 
              onSelectRow={selectZone} 
              className="zones-table" 
              items={zones} 
              headers={zoneHeaders}
              subObject="sectors" 
              subLabel="Sectores"
              totalItems={totalZones}
              filterTable = {filterZones}
              buttons={getZoneButton(() => selectZone({}))} 
            /> :
            <Spinner className="spinner-wrapper" animation="border" role="status">
              <span className="sr-only">Loading...</span>
            </Spinner>
          }
          
        </div> :  
        <div className='zone-form_wrapper'>
          <CustomForm 
            formName="customZone" 
            values={ selectedZone } 
            items={ ZONE_FORMS } 
            updateAction = { updateAttributes }
            buttons={
              getZoneActions(
                () => updateZone(updatedZone), 
                () => cancelZone(), 
                () => removeZone(selectedZone)
            )} 
            />
        </div>
        }
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    zoneHeaders: state.zoneHeaders,
    zones: getZones(state),
    selectedZone: getSelectedZone(state),
    deleteZone: getDeleteZone(state),
    updatedZone: getFormZone(state),
    totalZones: getTotalZones(state),
  };
};

const mapDispachToProps = dispatch => {
  return { 
    getZones: () => dispatch({ type: GET_ZONES, value: 1 }),
    updateAttributes: (value) => dispatch({ type: UPDATE_FIELD_ATTRIBUTE, value }),
    selectZone: (zone) => dispatch({ type: SELECT_ZONE_INIT, value: zone }),
    removeZone: (zone) => dispatch({ type: DELETE_ZONE_INIT, value: zone }),
    updateZone: (zone) => dispatch({ type: UPDATE_ZONE_INIT, value: zone }),
    cancelZone: () => dispatch({ type: CANCEL_ZONE_INIT}),
    filterZones: (prop) => dispatch({ type: FILTER_ZONE_INIT, value: prop }),
  };
};

export default connect(
  mapStateToProps,
  mapDispachToProps
)(ComponentZone);