import { createStructuredSelector } from 'reselect';

export const getTypes = (state) => state.types;
export const getSelectedType = (state) => state.selectedType;
export const getDeleteType = (state) => state.deleteSuccess;
export const getFormType = (state) => state.formType;
export const getTotalTypes = (state) => state.totalTypes;

export const typeSelector = createStructuredSelector({
    types: getTypes,
    selectedType: getSelectedType,
    deleteSuccess: getDeleteType,
    formType: getFormType
});