import { 
  SELECT_BASE_SUCCESS,
  SELECT_BASE_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_BASE_SUCCESS,
  UPDATE_BASE_ERROR,
  CANCEL_BASE_INIT,
  BASE_LIST,
  BASE_HEADERS,
  GET_BASES_SUCCESS,
  DELETE_BASE_SUCCESS,
  DELETE_BASE_ERROR,
  ORDER_BASE_SUCCESS,
  FILTER_BASE_SUCCESS
} from './constants';
  
  export const initialState = {
    bases: BASE_LIST,
    totalBases: [],
    baseHeaders: BASE_HEADERS,
    selectedBase: false, 
    deleteSuccess: false,
    formBase: {}
  };
  
  const reducer = (state = initialState, action) => {
    const newState = { ...state };
    const { payload } = action;
    const { bases } = payload ? payload : { bases: false };

    switch (action.type) {
      case GET_BASES_SUCCESS:
          newState.bases = bases;
          newState.totalBases = bases;
          
          return {
            ...newState
          };
      case SELECT_BASE_INIT:    
        return {
          ...newState
        };

      case SELECT_BASE_SUCCESS:  
        newState.selectedBase = payload;   
        newState.deleteSuccess = false;  
        newState.formBase = payload; 
        return {
          ...newState
        };
        
      case DELETE_BASE_SUCCESS: 
        newState.selectedBase = false; 
        newState.deleteSuccess = payload; 
        return {
          ...newState
        };

      case UPDATE_FIELD_ATTRIBUTE:
        const { value: { fieldName, fieldValue } } = action;
        let newFormObject = {...newState.selectedBase};
        newFormObject[fieldName] = fieldValue;
        newState.deleteSuccess = false;
        newState.formBase = newFormObject;
        newState.selectedBase = newFormObject;

        return {
          ...newState
        };
      case UPDATE_BASE_SUCCESS:
        newState.deleteSuccess = payload;
        newState.formBase = {};
        newState.selectedBase = false;

        return {
          ...newState
        };

        case UPDATE_BASE_ERROR:
          newState.deleteSuccess = payload;
          newState.formBase = {};
          newState.selectedBase = false;
  
          return {
            ...newState
          };
        
        case DELETE_BASE_ERROR:
          newState.deleteSuccess = payload;
          newState.formBase = {};
          newState.selectedBase = false;
    
          return {
            ...newState
          };  

        case CANCEL_BASE_INIT:
          newState.deleteSuccess = false;
          newState.formBase = {};
          newState.selectedBase = false;
  
          return {
            ...newState
          };
        case ORDER_BASE_SUCCESS:
            newState.deleteSuccess = false;
            newState.formBase = {};
            newState.selectedBase = false;
            newState.bases = bases;
            return {
              ...newState
            };
        case FILTER_BASE_SUCCESS:
              newState.deleteSuccess = false;
              newState.formBase = {};
              newState.selectedBase = false;
              newState.bases = bases;
              return {
                ...newState
              };            
      default:
        return {
          ...newState
        };
    }
  };
  
  export default reducer;
  