import React, { Component } from 'react';
import { ITEMS, ITEM_TITLE, ITEM_LOGOUT, getUserItem } from './constants';

import './sidebar.css';
import NavItem from './navitem';
import auth from '../../controllers/session';
import Nav from 'react-bootstrap/Nav';

export default class SideNav extends Component {
    constructor(props) {
        super(props);

        this.state = {
            activePath: '/',
            items: ITEMS
        }
    }

    onItemClick = (path) => {
        this.setState({ activePath: path });
    }

    render() {
        const { items } = this.state;
        const name = auth.getUserAttribute('name');     
        const picture = auth.getUserAttribute('picture');

        return (
            <div className='side-nav-wrapper'>
            <Nav className="side-nav-nar" activeKey="1" onSelect={this.onItemClick} as="ul">
                <NavItem
                    items={ITEM_TITLE}
                />
                <NavItem
                    items={getUserItem(name, picture)}
                />
                <NavItem
                    items={items}
                    onItemClick={() => this.onItemClick}
                />
                <NavItem
                    items={ITEM_LOGOUT}
                />
            </Nav>
            </div>
        );
    }
}
