import { takeLatest, put, call } from "redux-saga/effects";
import { GET_STATUS_API, PATH_API } from '../../Constants';
import { 
  GET_STATUS,
  SELECT_STATU_INIT,
  DELETE_STATU_INIT,
  UPDATE_STATU_INIT,
  ORDER_STATU_INIT, 
  FILTER_STATU_INIT
} from './constants';
import { 
  selectStatuSuccess,
  getStatusSuccess,
  getStatusError, 
  deleteStatuSuccess,
  deleteStatuError,
  updateStatuSuccess,
  updateStatuError,
  orderStatusSuccess,
  filterStatusSuccess
} from './actions';

function* selectStatu(action) {
  const { value } = action;

  yield put(selectStatuSuccess(value));
  return;
}

function* getStatus() {
  const urlStatus = `${ PATH_API }${GET_STATUS_API}`;

  const response = yield call(fetch, urlStatus, {
      method: 'GET',
  });

  if (response.status >= 200 && response.status < 300) {
    const successResponse = yield call([response, response.json]);
    console.log('successResponse', successResponse);
    yield put(getStatusSuccess(successResponse));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(getStatusError(errorResponse));
}

function* removeStatu(action) {
  const { value } = action;
  const { id } = value ? value : {};

  const urlStatus = `${ PATH_API }${GET_STATUS_API}/${id}`;

  const response = yield call(fetch, urlStatus, {
      method: 'DELETE',
  });

  if (response.status >= 200 && response.status < 300) {
    value.message = "Direccin eliminada exitosamente"; 
    yield put(deleteStatuSuccess(value));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(deleteStatuError(errorResponse));
}

function* updateStatu(action) {
  const { value } = action;
  const { id } = value ? value : {};
  
  const statuToUpdate = { ...value };

  value.message = "Direccin creada exitosamente"; 
  let statuId = id ? id : "0";
  let urlStatus = `${ PATH_API }${GET_STATUS_API}`;

  const getResponse = yield call(fetch, `${urlStatus}/${statuId}`, {
    method: 'GET'
  });

  const callBody = {
    method: 'POST',
    body: JSON.stringify(statuToUpdate)
  }

  if (getResponse.status >= 200 && getResponse.status < 300) {
    value.message = "Direccin actualizada exitosamente"; 
    callBody.method = 'PUT';
    urlStatus = `${urlStatus}/${id}`
  }

  const response = yield call(fetch, urlStatus, {
      ...callBody
  });

  if (response.status >= 200 && response.status < 300) {
    yield put(updateStatuSuccess(value));
    return;
  }
  
  const errorResponse = yield call([response, response.json]);
  value.message = "Hubo un problema creando los cambios"; 
  yield put(updateStatuError(errorResponse));
}

function* orderStatu(action) { 
  const { value: {column, items, sense} } = action;

  items.sort((a,b) => {
    if(a[column] < b[column]) {
      return sense;
    }
    if(a[column] > b[column]) {
      return -1*sense;
    }
    return 0;
  })

  const response = {
    status: items 
  }

  yield put(orderStatusSuccess(response));
}

function* filterStatu(action) { 
  const { value: {fieldValue, items} } = action;
  
  let filtered = items.filter(item => {
    let itemFiltered = false;

    Object.keys(item).map(key => {
      const keyToFilter = item[key] && typeof item[key] !== 'object' ? item[key].toString().toLowerCase() : ""; 
      itemFiltered = itemFiltered || keyToFilter.includes(fieldValue);
      return true;
    })

    return itemFiltered;
  });

  const response = {
    status: filtered
  }

  yield put(filterStatusSuccess(response));
}

export function* watchStatu() {
  yield takeLatest(GET_STATUS, getStatus);
  yield takeLatest(SELECT_STATU_INIT, selectStatu);
  yield takeLatest(DELETE_STATU_INIT, removeStatu);
  yield takeLatest(UPDATE_STATU_INIT, updateStatu);
  yield takeLatest(ORDER_STATU_INIT, orderStatu);
  yield takeLatest(FILTER_STATU_INIT, filterStatu);
}
