import { takeLatest, put, call } from "redux-saga/effects";
import { GET_ADDRESSES_API, GET_LOCATIONS_API, PATH_API } from '../../Constants';
import { 
  GET_ADDRESSES,
  SELECT_ADDRESS_INIT,
  DELETE_ADDRESS_INIT,
  UPDATE_ADDRESS_INIT
} from './constants';
import { 
  selectAddressSuccess,
  getAddressesSuccess,
  getAddressesError, 
  deleteAddressSuccess,
  deleteAddressError,
  updateAddressSuccess,
  updateAddressError
} from './actions';

function* selectAddress(action) {
  const { value } = action;
  
  yield put(selectAddressSuccess(value));
  return;
}

function* getAddresses() {
  const urlAddresses = `${ PATH_API }${GET_ADDRESSES_API}`;

  const response = yield call(fetch, urlAddresses, {
      method: 'GET',
  });

  if (response.status >= 200 && response.status < 300) {
    const successResponse = yield call([response, response.json]);
    yield put(getAddressesSuccess(successResponse));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(getAddressesError(errorResponse));
}

function* removeAddress(action) {
  const { value } = action;
  const { id } = value ? value : {};

  const urlAddresses = `${ PATH_API }${GET_ADDRESSES_API}/${id}`;

  const response = yield call(fetch, urlAddresses, {
      method: 'DELETE',
  });

  if (response.status >= 200 && response.status < 300) {
    value.message = "Direccin eliminada exitosamente"; 
    yield put(deleteAddressSuccess(value));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(deleteAddressError(errorResponse));
}

function* updateAddress(action) {
  const { value } = action;
  const { id, latitude, longitude } = value ? value : {};

  const addressToUpdate = {...value}

  value.message = "Direccin creada exitosamente"; 
  let addressId = id ? id : "0";
  let addressLatitude = latitude ? parseInt(latitude) : 0;
  let addressLongitud = longitude ? parseInt(longitude) : 0;

  let urlAddresses = `${ PATH_API }${GET_ADDRESSES_API}`;

  const getResponse = yield call(fetch, `${urlAddresses}/${addressId}`, {
    method: 'GET'
  });

  addressToUpdate.longitude = addressLongitud;
  addressToUpdate.latitude = addressLatitude;

  const callBody = {
    method: 'POST',
    body: JSON.stringify(addressToUpdate)
  }

  if (getResponse.status >= 200 && getResponse.status < 300) {
    value.message = "Direccin actualizada exitosamente"; 
    callBody.method = 'PUT';
    urlAddresses = `${urlAddresses}/${id}`
  }

  const response = yield call(fetch, urlAddresses, {
      ...callBody
  });

  if (response.status >= 200 && response.status < 300) {
    yield put(updateAddressSuccess(value));
    return;
  }
  
  const errorResponse = yield call([response, response.json]);
  value.message = "Hubo un problema creando los cambios"; 
  yield put(updateAddressError(errorResponse));
}

export function* watchAddress() {
  yield takeLatest(GET_ADDRESSES, getAddresses);
  yield takeLatest(SELECT_ADDRESS_INIT, selectAddress);
  yield takeLatest(DELETE_ADDRESS_INIT, removeAddress);
  yield takeLatest(UPDATE_ADDRESS_INIT, updateAddress);
  
}
