import {
    UPDATE_FIELD_ATTRIBUTE,
    UPDATE_BASE_INIT,
    UPDATE_BASE_SUCCESS,
    UPDATE_BASE_ERROR,
    GET_BASES_SUCCESS,
    GET_BASES_ERROR,
    SELECT_BASE_INIT,
    SELECT_BASE_SUCCESS,
    SELECT_BASE_ERROR,
    DELETE_BASE_INIT,
    DELETE_BASE_SUCCESS,
    DELETE_BASE_ERROR,
    FILTER_BASE_INIT,
    FILTER_BASE_SUCCESS,
    FILTER_BASE_ERROR,
    ORDER_BASE_ERROR,
    ORDER_BASE_SUCCESS,
    ORDER_BASE_INIT
} from './constants'

export function updateBase (payload) {
    return {
        type: UPDATE_BASE_INIT,
        payload
    };
}

export function updateBaseSuccess (payload) {
    return {
        type: UPDATE_BASE_SUCCESS,
        payload
    };
}

export function updateBaseError (payload) {
    return {
        type: UPDATE_BASE_ERROR,
        payload
    };
}

export function updateAttributes (payload) {
    return {
        type: UPDATE_FIELD_ATTRIBUTE,
        payload
    };
}

export function selectBase(payload) {
    return {
        type: SELECT_BASE_INIT,
        payload
    };
}

export function selectBaseSuccess(payload) {
    return {
        type: SELECT_BASE_SUCCESS,
        payload,
    };
}

export function selectBaseError(payload) {
    return {
        type: SELECT_BASE_ERROR,
        payload,
    };
}

