import { createStructuredSelector } from 'reselect';

export const getZones = (state) => state.zones;
export const getSelectedZone = (state) => state.selectedZone;
export const getDeleteZone = (state) => state.deleteSuccess;
export const getFormZone = (state) => state.formZone;
export const getTotalZones = (state) => state.totalZones;

export const addressSelector = createStructuredSelector({
    zones: getZones,
    selectedZone: getSelectedZone,
    deleteSuccess: getDeleteZone,
    formZone: getFormZone
});