import React, { Component } from "react";
import "./style.css";
import { connect } from "react-redux";
import CustomForm from "../../components/customform/customform";
import CustomTable from "../../components/table/table";
import { getAttributes, getSelectedAttribute, getDeleteAttribute, getFormAttribute, getTotalAttributes } from "./selectors";
import { Spinner, Alert } from 'react-bootstrap';
import { 
  getAttributeActions,
  getAttributeButton,
  GET_ATTRIBUTES,
  SELECT_ATTRIBUTE_INIT, 
  ATTRIBUTE_FORMS, 
  DELETE_ATTRIBUTE_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_ATTRIBUTE_INIT,
  CANCEL_ATTRIBUTE_INIT,
  ORDER_ATTRIBUTE_INIT,
  FILTER_ATTRIBUTE_INIT,
} from './constants'


class ComponentAttribute extends Component {

  componentWillMount () {
    const { getAttributes } = this.props;
    getAttributes();
  }

  componentWillReceiveProps (nextProps) {
    const { getAttributes } = nextProps;

    if (nextProps.deleteAttribute && this.props.deleteAttribute !== nextProps.deleteAttribute) {
      getAttributes();
    }
  }

  render() {
    const {
      attributeHeaders, 
      attributes,
      selectAttribute,
      selectedAttribute,
      removeAttribute,
      deleteAttribute,
      updateAttributes,
      updatedAttribute,
      updateAttribute,
      cancelAttribute,
      orderAttribute,
      filterAttribute,
      totalAttributes
  } = this.props;

    return (
      <div className="App">
        { deleteAttribute ?
          <Alert  variant="success">
            {deleteAttribute.message}
          </Alert>
          : ''
        }

        {!selectedAttribute ? 
          <div className='attribute-list_wrapper'>
          { attributes && !attributes.emprty ? 
            <CustomTable 
              onSelectRow={selectAttribute} 
              className="attributes-table" 
              items={attributes}
              totalItems={totalAttributes}
              orderTable={orderAttribute}
              headers={attributeHeaders}
              filterTable = {filterAttribute}
              paginator = {true}
              buttons={getAttributeButton(() => selectAttribute({}))} 
            /> :
            <Spinner className="spinner-wrapper" animation="border" role="status">
              <span className="sr-only">Loading...</span>
            </Spinner>
          }
          
        </div> :  
        <div className='attribute-form_wrapper'>
          <CustomForm 
            formName="customAttribute" 
            values={ selectedAttribute } 
            items={ ATTRIBUTE_FORMS } 
            updateAction = { updateAttributes }
            buttons={
              getAttributeActions(
                () => updateAttribute(updatedAttribute), 
                () => cancelAttribute(), 
                () => removeAttribute(selectedAttribute)
            )} 
            />
        </div>
        }
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    attributeHeaders: state.attributeHeaders,
    attributes: getAttributes(state),
    selectedAttribute: getSelectedAttribute(state),
    deleteAttribute: getDeleteAttribute(state),
    updatedAttribute: getFormAttribute(state),
    totalAttributes: getTotalAttributes(state),
  };
};

const mapDispachToProps = dispatch => {
  return { 
    getAttributes: () => dispatch({ type: GET_ATTRIBUTES, value: 1 }),
    updateAttributes: (value) => dispatch({ type: UPDATE_FIELD_ATTRIBUTE, value }),
    selectAttribute: (attribute) => dispatch({ type: SELECT_ATTRIBUTE_INIT, value: attribute }),
    removeAttribute: (attribute) => dispatch({ type: DELETE_ATTRIBUTE_INIT, value: attribute }),
    updateAttribute: (attribute) => dispatch({ type: UPDATE_ATTRIBUTE_INIT, value: attribute }),
    cancelAttribute: () => dispatch({ type: CANCEL_ATTRIBUTE_INIT}),
    orderAttribute: (prop) => dispatch({ type: ORDER_ATTRIBUTE_INIT, value: prop }),
    filterAttribute: (prop) => dispatch({ type: FILTER_ATTRIBUTE_INIT, value: prop }),
  };
};

export default connect(
  mapStateToProps,
  mapDispachToProps
)(ComponentAttribute);