export const getWorkerButton = (handlerSafe) => {
    return [
        {
            label: "Nuevo Trabajador",
            className: "",
            action: false,
            style: "primary",
            onClick: handlerSafe
        }
    ]
}

export const getWorkerActions = (handlerSave, handlerClean, handlerRemove) => {
    return [
        {
            label: "Guardar Cambios",
            className: "",
            action: false,
            style: "primary",
            variant: "outlined",
            onClick: handlerSave
        },
        {
            label: "Eliminar",
            className: "",
            action: false,
            style: "default",
            variant: "outlined",
            onClick: handlerRemove
        },
        {
            label: "Cancelar",
            className: "",
            action: false,
            style: "secondary",
            variant: "outlined",
            onClick: handlerClean
        },
    ];
}

