import React, { Component } from 'react';
import { Card } from 'react-bootstrap';
import CustomReduxForm from './customReduxForm';
import CustomButton  from '../custombutton/button';
import { connect } from "react-redux";

class CustomForm extends Component {
    render() {
        const { items, formTitle, className, formName, values, buttons, updateAction } = this.props;

        return (
            <Card key={`card-${formName}`} className={className}>
                <Card.Header>
                    {formTitle}
                </Card.Header>
                <Card.Body>
                    <CustomReduxForm items={items} formName={formName} dataObject={values} updateAction={updateAction} />
                    <CustomButton items={buttons} />
                </Card.Body>
            </Card>
        );
    }
}

function mapStateToProps(storeState, componentProps) {
    const { formData } = storeState;
    return { formData };
}

export default connect(mapStateToProps)(CustomForm);