import React from 'react'
import { reduxForm } from 'redux-form';
import { Form, InputGroup } from 'react-bootstrap';
import CustomField from './customfield';

const CustomReduxForm = (props) => {
  const { items, formName, dataObject, updateAction } = props;

  return (
        <Form key={`form-${formName}`} name={formName}>
          { dataObject && items && items.map((field, j) => {
              return <InputGroup key={j} className="form-custom-wrapper">
                          <CustomField
                              id={j}
                              label={field.label} 
                              hint={field.hint}
                              type={field.type}
                              typeForm={field.typeForm}
                              value={dataObject[field.name]}
                              options={field.options}
                              disable={field.disable} 
                              placeholder={field.placeholder}
                              updateAction={updateAction}
                              name={field.name}
                          />
                      </InputGroup>
          }
        )}
      </Form>
  ) 
}

export default reduxForm({
  form: "customSector",              // <------ same form name
  destroyOnUnmount: false,     // <------ preserve form data
})(CustomReduxForm)