import { takeLatest, put, call } from "redux-saga/effects";
import { GET_WORKERS_API, PATH_API } from '../../Constants';
import { 
  GET_WORKERS,
  SELECT_WORKER_INIT,
  DELETE_WORKER_INIT,
  UPDATE_WORKER_INIT,
  ORDER_WORKER_INIT, 
  FILTER_WORKER_INIT
} from './constants';
import { 
  selectWorkerSuccess,
  getWorkersSuccess,
  getWorkersError, 
  deleteWorkerSuccess,
  deleteWorkerError,
  updateWorkerSuccess,
  updateWorkerError,
  orderWorkersSuccess,
  filterWorkersSuccess
} from './actions';

function* selectWorker(action) {
  const { value } = action;

  yield put(selectWorkerSuccess(value));
  return;
}

function* getWorkers() {
  const urlWorkers = `${ PATH_API }${GET_WORKERS_API}`;

  const response = yield call(fetch, urlWorkers, {
      method: 'GET',
  });

  if (response.status >= 200 && response.status < 300) {
    const successResponse = yield call([response, response.json]);

    yield put(getWorkersSuccess(successResponse));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(getWorkersError(errorResponse));
}

function* removeWorker(action) {
  const { value } = action;
  const { id } = value ? value : {};

  const urlWorkers = `${ PATH_API }${GET_WORKERS_API}/${id}`;

  const response = yield call(fetch, urlWorkers, {
      method: 'DELETE',
  });

  if (response.status >= 200 && response.status < 300) {
    value.message = "Direccin eliminada exitosamente"; 
    yield put(deleteWorkerSuccess(value));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(deleteWorkerError(errorResponse));
}

function* updateWorker(action) {
  const { value } = action;
  const { id } = value ? value : {};
  
  const workerToUpdate = { ...value };

  value.message = "Direccin creada exitosamente"; 
  let workerId = id ? id : "0";
  let urlWorkers = `${ PATH_API }${GET_WORKERS_API}`;

  const getResponse = yield call(fetch, `${urlWorkers}/${workerId}`, {
    method: 'GET'
  });

  const callBody = {
    method: 'POST',
    body: JSON.stringify(workerToUpdate)
  }

  if (getResponse.status >= 200 && getResponse.status < 300) {
    value.message = "Direccin actualizada exitosamente"; 
    callBody.method = 'PUT';
    urlWorkers = `${urlWorkers}/${id}`
  }

  const response = yield call(fetch, urlWorkers, {
      ...callBody
  });

  if (response.status >= 200 && response.status < 300) {
    yield put(updateWorkerSuccess(value));
    return;
  }
  
  const errorResponse = yield call([response, response.json]);
  value.message = "Hubo un problema creando los cambios"; 
  yield put(updateWorkerError(errorResponse));
}

function* orderWorker(action) { 
  const { value: {column, items, sense} } = action;

  items.sort((a,b) => {
    if(a[column] < b[column]) {
      return sense;
    }
    if(a[column] > b[column]) {
      return -1*sense;
    }
    return 0;
  })

  const response = {
    workers: items 
  }

  yield put(orderWorkersSuccess(response));
}

function* filterWorker(action) { 
  const { value: {fieldValue, items} } = action;
  
  let filtered = items.filter(item => {
    let itemFiltered = false;

    Object.keys(item).map(key => {
      const keyToFilter = item[key] && typeof item[key] !== 'object' ? item[key].toString().toLowerCase() : ""; 
      itemFiltered = itemFiltered || keyToFilter.includes(fieldValue);
      return true;
    })

    return itemFiltered;
  });

  const response = {
    workers: filtered
  }

  yield put(filterWorkersSuccess(response));
}

export function* watchWorker() {
  yield takeLatest(GET_WORKERS, getWorkers);
  yield takeLatest(SELECT_WORKER_INIT, selectWorker);
  yield takeLatest(DELETE_WORKER_INIT, removeWorker);
  yield takeLatest(UPDATE_WORKER_INIT, updateWorker);
  yield takeLatest(ORDER_WORKER_INIT, orderWorker);
  yield takeLatest(FILTER_WORKER_INIT, filterWorker);
}
