import React, { Component } from "react";
import "./style.css";
import { connect } from "react-redux";
import CustomForm from "../../components/customform/customform";
import CustomTable from "../../components/table/table";
import { getWorkers, getSelectedWorker, getDeleteWorker, getFormWorker, getTotalWorkers } from "./selectors";
import { Spinner, Alert } from 'react-bootstrap';
import { 
  getWorkerActions,
  getWorkerButton,
  GET_WORKERS,
  SELECT_WORKER_INIT, 
  WORKER_FORMS, 
  DELETE_WORKER_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_WORKER_INIT,
  CANCEL_WORKER_INIT,
  ORDER_WORKER_INIT,
  FILTER_WORKER_INIT,
} from './constants'


class ComponentWorker extends Component {

  componentWillMount () {
    const { getWorkers } = this.props;
    getWorkers();
  }

  componentWillReceiveProps (nextProps) {
    const { getWorkers } = nextProps;

    if (nextProps.deleteWorker && this.props.deleteWorker !== nextProps.deleteWorker) {
      getWorkers();
    }
  }

  render() {
    const {
      workerHeaders, 
      workers,
      selectWorker,
      selectedWorker,
      removeWorker,
      deleteWorker,
      updateWorkers,
      updatedWorker,
      updateWorker,
      cancelWorker,
      orderWorker,
      filterWorker,
      totalWorkers
  } = this.props;

    return (
      <div className="App">
        { deleteWorker ?
          <Alert  variant="success">
            {deleteWorker.message}
          </Alert>
          : ''
        }

        {!selectedWorker ? 
          <div className='worker-list_wrapper'>
          { workers && !workers.emprty ? 
            <CustomTable 
              onSelectRow={selectWorker} 
              className="workers-table" 
              items={workers}
              totalItems={totalWorkers}
              orderTable={orderWorker}
              headers={workerHeaders}
              filterTable = {filterWorker}
              paginator = {true}
              buttons={getWorkerButton(() => selectWorker({}))} 
            /> :
            <Spinner className="spinner-wrapper" animation="border" role="status">
              <span className="sr-only">Loading...</span>
            </Spinner>
          }
          
        </div> :  
        <div className='worker-form_wrapper'>
          <CustomForm 
            formName="customWorker" 
            values={ selectedWorker } 
            items={ WORKER_FORMS } 
            updateAction = { updateWorkers }
            buttons={
              getWorkerActions(
                () => updateWorker(updatedWorker), 
                () => cancelWorker(), 
                () => removeWorker(selectedWorker)
            )} 
            />
        </div>
        }
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    workerHeaders: state.workerHeaders,
    workers: getWorkers(state),
    selectedWorker: getSelectedWorker(state),
    deleteWorker: getDeleteWorker(state),
    updatedWorker: getFormWorker(state),
    totalWorkers: getTotalWorkers(state),
  };
};

const mapDispachToProps = dispatch => {
  return { 
    getWorkers: () => dispatch({ type: GET_WORKERS, value: 1 }),
    updateWorkers: (value) => dispatch({ type: UPDATE_FIELD_ATTRIBUTE, value }),
    selectWorker: (worker) => dispatch({ type: SELECT_WORKER_INIT, value: worker }),
    removeWorker: (worker) => dispatch({ type: DELETE_WORKER_INIT, value: worker }),
    updateWorker: (worker) => dispatch({ type: UPDATE_WORKER_INIT, value: worker }),
    cancelWorker: () => dispatch({ type: CANCEL_WORKER_INIT}),
    orderWorker: (prop) => dispatch({ type: ORDER_WORKER_INIT, value: prop }),
    filterWorker: (prop) => dispatch({ type: FILTER_WORKER_INIT, value: prop }),
  };
};

export default connect(
  mapStateToProps,
  mapDispachToProps
)(ComponentWorker);