import {
    UPDATE_FIELD_ATTRIBUTE,
    UPDATE_ADDRESS_INIT,
    UPDATE_ADDRESS_SUCCESS,
    UPDATE_ADDRESS_ERROR,
    GET_ADDRESSES_SUCCESS,
    GET_ADDRESSES_ERROR,
    SELECT_ADDRESS_INIT,
    SELECT_ADDRESS_SUCCESS,
    SELECT_ADDRESS_ERROR,
    DELETE_ADDRESS_INIT,
    DELETE_ADDRESS_SUCCESS,
    DELETE_ADDRESS_ERROR
} from './constants'

export function updateAddress (payload) {
    return {
        type: UPDATE_ADDRESS_INIT,
        payload
    };
}

export function updateAddressSuccess (payload) {
    return {
        type: UPDATE_ADDRESS_SUCCESS,
        payload
    };
}

export function updateAddressError (payload) {
    return {
        type: UPDATE_ADDRESS_ERROR,
        payload
    };
}

export function updateAttributes (payload) {
    return {
        type: UPDATE_FIELD_ATTRIBUTE,
        payload
    };
}

export function selectAddress(payload) {
    return {
        type: SELECT_ADDRESS_INIT,
        payload
    };
}

export function selectAddressSuccess(payload) {
    return {
        type: SELECT_ADDRESS_SUCCESS,
        payload,
    };
}

export function selectAddressError(payload) {
    return {
        type: SELECT_ADDRESS_ERROR,
        payload,
    };
}

/* DELETE ADDRESS */
export function deleteAddress(payload) {
    return {
        type: DELETE_ADDRESS_INIT,
        payload
    };
}

export function deleteAddressSuccess(payload) {
    return {
        type: DELETE_ADDRESS_SUCCESS,
        payload,
    };
}

export function deleteAddressError(payload) {
    return {
        type: DELETE_ADDRESS_ERROR,
        payload,
    };
}

export function getAddressesSuccess(payload) {
    return {
        type: GET_ADDRESSES_SUCCESS,
        payload,
    };
}

export function getAddressesError(payload) {
    return {
        type: GET_ADDRESSES_ERROR,
        payload,
    };
}