import React, { Component } from 'react';
import Container from 'react-bootstrap/Container'
import "./profile.css";
import { getComponentItems } from "./constant";
import CustomCard from '../../components/customcard/customcard';
import auth from '../../controllers/session';

export default class Profile extends Component {
  render() {
    const name = auth.getUserAttribute('name');
    const picture = auth.getUserAttribute('picture');

    return (
      <Container className='main-wrapper profile'>
        <h1> { name } </h1>
        <div className="text-wrapper">
          <CustomCard items={getComponentItems(picture)} />
        </div>
      </Container>
    );
  }
}