import React, { Component } from 'react';
import { Form } from 'react-bootstrap';
import { Input, Select, Switch, MenuItem, TextField  } from '@material-ui/core';

export default class CustomField extends Component {

    constructor(props) {
        super(props);

        this.state = {
            fieldValue: props.value,
            fieldName: props.name
        }
    }

    componentWillReceiveProps(nextProps) {
        if(nextProps.fieldValue) {
            this.setState({
                fieldValue: nextProps.fieldValue,
                fieldName: nextProps.fieldName
            });
        }
    };
    
    onChange = (e) => {
        let targetValue = e.target.value;

        this.setState({
            fieldValue: targetValue,
            fieldName: e.target.name
        })
    };

    onBlur = (e) => {
        const { fieldValue, fieldName } = this.state;

        this.props.updateAction({
            fieldValue,
            fieldName
        })
    };

    onToggleChange = (e) => {
        let targetValue = e.target.value;
        const targetName = e.target.name;

        targetValue = typeof targetValue === "boolean" ? targetValue : targetValue === "true";

        this.setState({
            fieldValue: !targetValue,
            fieldName: targetName
        })

        this.props.updateAction({
            fieldValue: !targetValue,
            fieldName: targetName
        })
    }

    onFilterValue = (e) => {
        const { items } = this.props;
        const targetValue = e.target.value;
        const targetName = e.target.name;

        this.setState({
            fieldValue: targetValue,
            fieldName: targetName
        })

        this.props.updateAction({
            fieldValue: targetValue,
            fieldName: targetName, 
            items: items
        })
    }

    selectInput = (type, typeForm, options, value, label, placeholder, name, key, disable) => {
        let formControl = false;
        const customKey = `input-${label}-${key}`;
        if (type === "toggle" && typeof value !== "boolean") {
            value = value === "true";
        }

        switch (typeForm) {
            case "filter":
                formControl = 
                    <TextField 
                        className="custom-input"
                        key={customKey}  
                        name={name} 
                        label={placeholder}
                        value={value} 
                        type={type} 
                        onChange={this.onFilterValue}
                        disabled={disable}
                    />;
                break;  
            case "area":
                formControl = 
                    <Input
                        key={customKey} 
                        className="custom-input" 
                        name={name} 
                        rows="3" 
                        as="textarea" 
                        label={label} 
                        type={type} 
                        value={value} 
                        onBlur={this.onBlur}
                        onChange={this.onChange}
                        disabled={disable}
                    />;
                break;    
            case "selections":
                formControl = 
                    <div className="check-control"> 
                        {options.map((type, i) => (
                            <Form.Check 
                                type={type.type}
                                id={`${type.type}-${i}`}
                                label={type.label}
                            />
                        ))}
                    </div>
                break;
            case "toggle" :
                formControl = 
                    <Switch 
                        size="medium" 
                        checked={value}
                        name={name}
                        value={value} 
                        onChange={this.onToggleChange}
                        disabled={disable}
                    />;
                return formControl;            
            case "select":
                formControl = 
                <Select
                    name={name}
                    key={customKey} 
                    as="select"
                    className="custom-input mr-sm-2"
                    id="inlineFormCustomSelect"
                    value={value} 
                    onBlur={this.onBlur}
                    onChange={this.onChange}
                    disabled={disable}
                >
                    {options.map((type, i) => {
                        return (
                            <MenuItem 
                                key={`option-${i}`}
                                value={type.value}
                                id={`option-${i}`}>
                                    {type.label}
                            </MenuItem>
                        )
                    })}
                </Select>
                break; 
            case "multiple":
                
                break;      
            default:
                formControl = 
                    <TextField 
                        className="custom-input"
                        key={customKey}  
                        name={name} 
                        label={placeholder}
                        value={value} 
                        type={type} 
                        onBlur={this.onBlur}
                        onChange={this.onChange}
                        disabled={disable}
                    />;
                break;
        }

        return formControl;
    }

    render() {
        const { 
            placeholder, 
            label, 
            type, 
            typeForm, 
            options,
            key, 
            name,
            disable 
        } = this.props;

        const customKey = label ? label.replace(/\s/g, "").toLowerCase() : "";
        const { fieldValue } = this.state;

        return (
            <div key={customKey} className="form-custom-group">
                <label className="label-name">
                    <span className="content-name">{label}</span>
                </label>
                { this.selectInput(type, typeForm, options, fieldValue, label, placeholder, name, key, disable) }
            </div >
        );
    }
}
