import React, { Component } from "react";
import "./style.css";
import { connect } from "react-redux";
import CustomForm from "../../components/customform/customform";
import CustomTable from "../../components/table/table";
import { getBases, getSelectedBase, getDeleteBase, getFormBase, getTotalBases } from "./selectors";
import { Spinner, Alert } from 'react-bootstrap';
import { 
  getBaseActions,
  getBaseButton,
  GET_BASES,
  SELECT_BASE_INIT, 
  BASE_FORMS, 
  DELETE_BASE_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_BASE_INIT,
  CANCEL_BASE_INIT,
  ORDER_BASE_INIT,
  FILTER_BASE_INIT,
} from './constants'


class ComponentBase extends Component {

  componentWillMount () {
    const { getBases } = this.props;
    getBases();
  }

  componentWillReceiveProps (nextProps) {
    const { getBases } = nextProps;

    if (nextProps.deleteBase && this.props.deleteBase !== nextProps.deleteBase) {
      getBases();
    }
  }

  render() {
    const {
      baseHeaders, 
      bases,
      selectBase,
      selectedBase,
      removeBase,
      deleteBase,
      updateAttributes,
      updatedBase,
      updateBase,
      cancelBase,
      orderBase,
      filterBase,
      totalBases
  } = this.props;

    return (
      <div className="App">
        { deleteBase ?
          <Alert  variant="success">
            {deleteBase.message}
          </Alert>
          : ''
        }

        {!selectedBase ? 
          <div className='base-list_wrapper'>
          { bases && !bases.emprty ? 
            <CustomTable 
              onSelectRow={selectBase} 
              className="bases-table" 
              items={bases}
              totalItems={totalBases}
              subObject="zones" 
              subLabel="Zonas"
              childObject="sectors"
              childLabel="Sectores" 
              orderTable={orderBase}
              headers={baseHeaders}
              filterTable = {filterBase}
              paginator = {true}
              buttons={getBaseButton(() => selectBase({}))} 
            /> :
            <Spinner className="spinner-wrapper" animation="border" role="status">
              <span className="sr-only">Loading...</span>
            </Spinner>
          }
          
        </div> :  
        <div className='base-form_wrapper'>
          <CustomForm 
            formName="customBase" 
            values={ selectedBase } 
            items={ BASE_FORMS } 
            updateAction = { updateAttributes }
            buttons={
              getBaseActions(
                () => updateBase(updatedBase), 
                () => cancelBase(), 
                () => removeBase(selectedBase)
            )} 
            />
        </div>
        }
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    baseHeaders: state.baseHeaders,
    bases: getBases(state),
    selectedBase: getSelectedBase(state),
    deleteBase: getDeleteBase(state),
    updatedBase: getFormBase(state),
    totalBases: getTotalBases(state),
  };
};

const mapDispachToProps = dispatch => {
  return { 
    getBases: () => dispatch({ type: GET_BASES, value: 1 }),
    updateAttributes: (value) => dispatch({ type: UPDATE_FIELD_ATTRIBUTE, value }),
    selectBase: (base) => dispatch({ type: SELECT_BASE_INIT, value: base }),
    removeBase: (base) => dispatch({ type: DELETE_BASE_INIT, value: base }),
    updateBase: (base) => dispatch({ type: UPDATE_BASE_INIT, value: base }),
    cancelBase: () => dispatch({ type: CANCEL_BASE_INIT}),
    orderBase: (prop) => dispatch({ type: ORDER_BASE_INIT, value: prop }),
    filterBase: (prop) => dispatch({ type: FILTER_BASE_INIT, value: prop }),
  };
};

export default connect(
  mapStateToProps,
  mapDispachToProps
)(ComponentBase);