    STATE_AGE_DOWN, 
    STATE_GET_USER_ASYNC, 
    GET_USER_SUCCESS, 
    GET_USER_ERROR,
    UPDATE_FIELD_ATTRIBUTE,
    UPDATE_SECTOR_INIT,
    UPDATE_SECTOR_SUCCESS,
