export function updateSector (payload) {
    return {
        type: UPDATE_SECTOR_INIT,
        payload
    };
}

export function updateSectorSuccess (payload) {
    return {
        type: UPDATE_SECTOR_SUCCESS,
        payload
    };
}

export function updateSectorError (payload) {
    return {
        type: UPDATE_SECTOR_ERROR,
        payload
    };
}

