import { createStructuredSelector } from 'reselect';
import { initialState } from './reducer';

export const getAge = () => initialState.age;
export const getEmail = () => initialState.email;
export const getSectors = (state) => state.sectors;
export const getSelectedSector = (state) => state.selectedSector;
export const getDeleteSector = (state) => state.deleteSuccess;
export const getFormSector = (state) => state.formSector;
export const getTotalSectors = (state) => state.totalSectors;

export const userSelector = createStructuredSelector({
    age: getAge,
    email: getEmail,
    sectors: getSectors,
    totalSectors: getTotalSectors,
});