import { takeLatest, put, call } from "redux-saga/effects";
import { GET_BASES_API, PATH_API, GET_SECTORS_API } from '../../Constants';
import { 
  GET_BASES,
  SELECT_BASE_INIT,
  DELETE_BASE_INIT,
  UPDATE_BASE_INIT,
  ORDER_BASE_INIT, 
  FILTER_BASE_INIT
} from './constants';
import { 
  selectBaseSuccess,
  getBasesSuccess,
  getBasesError, 
  deleteBaseSuccess,
  deleteBaseError,
  updateBaseSuccess,
  updateBaseError,
  orderBasesSuccess,
  filterBasesSuccess
} from './actions';

function* selectBase(action) {
  const { value } = action;

  yield put(selectBaseSuccess(value));
  return;
}

function* getBases() {
  const urlBases = `${ PATH_API }${GET_BASES_API}`;
  const urlSectores = `${ PATH_API }${GET_SECTORS_API}`;

  const response = yield call(fetch, urlBases, {
      method: 'GET',
  });

  const responseSectores = yield call(fetch, urlSectores, {
    method: 'GET',
});

  if (response.status >= 200 && response.status < 300) {
    const successResponse = yield call([response, response.json]);

    if (responseSectores.status >= 200 && responseSectores.status < 300) { 
      const successSectores = yield call([responseSectores, responseSectores.json]);

      const { sectors } = successSectores;
      successResponse.bases.map((key) => {
        key.zones.map((zone) => {
          zone.sectors = sectors;
        });
      });
    }

    yield put(getBasesSuccess(successResponse));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(getBasesError(errorResponse));
}

function* removeBase(action) {
  const { value } = action;
  const { id } = value ? value : {};

  const urlBases = `${ PATH_API }${GET_BASES_API}/${id}`;

  const response = yield call(fetch, urlBases, {
      method: 'DELETE',
  });

  if (response.status >= 200 && response.status < 300) {
    value.message = "Direccin eliminada exitosamente"; 
    yield put(deleteBaseSuccess(value));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(deleteBaseError(errorResponse));
}

function* updateBase(action) {
  const { value } = action;
  const { id } = value ? value : {};
  
  const baseToUpdate = { ...value };

  value.message = "Direccin creada exitosamente"; 
  let baseId = id ? id : "0";
  let urlBases = `${ PATH_API }${GET_BASES_API}`;

  const getResponse = yield call(fetch, `${urlBases}/${baseId}`, {
    method: 'GET'
  });

  const callBody = {
    method: 'POST',
    body: JSON.stringify(baseToUpdate)
  }

  if (getResponse.status >= 200 && getResponse.status < 300) {
    value.message = "Direccin actualizada exitosamente"; 
    callBody.method = 'PUT';
    urlBases = `${urlBases}/${id}`
  }

  const response = yield call(fetch, urlBases, {
      ...callBody
  });

  if (response.status >= 200 && response.status < 300) {
    yield put(updateBaseSuccess(value));
    return;
  }
  
  const errorResponse = yield call([response, response.json]);
  value.message = "Hubo un problema creando los cambios"; 
  yield put(updateBaseError(errorResponse));
}

function* orderBase(action) { 
  const { value: {column, items, sense} } = action;

  items.sort((a,b) => {
    if(a[column] < b[column]) {
      return sense;
    }
    if(a[column] > b[column]) {
      return -1*sense;
    }
    return 0;
  })

  const response = {
    bases: items 
  }

  yield put(orderBasesSuccess(response));
}

function* filterBase(action) { 
  const { value: {fieldValue, items} } = action;
  
  let filtered = items.filter(item => {
    let itemFiltered = false;

    Object.keys(item).map(key => {
      const keyToFilter = item[key] && typeof item[key] !== 'object' ? item[key].toString().toLowerCase() : ""; 
      itemFiltered = itemFiltered || keyToFilter.includes(fieldValue);
      return true;
    })

    return itemFiltered;
  });

  const response = {
    bases: filtered
  }

  yield put(filterBasesSuccess(response));
}

export function* watchBase() {
  yield takeLatest(GET_BASES, getBases);
  yield takeLatest(SELECT_BASE_INIT, selectBase);
  yield takeLatest(DELETE_BASE_INIT, removeBase);
  yield takeLatest(UPDATE_BASE_INIT, updateBase);
  yield takeLatest(ORDER_BASE_INIT, orderBase);
  yield takeLatest(FILTER_BASE_INIT, filterBase);
}
