import { takeLatest, put, call } from "redux-saga/effects";
import { GET_ATTRIBUTES_API, PATH_API } from '../../Constants';
import { 
  GET_ATTRIBUTES,
  SELECT_ATTRIBUTE_INIT,
  DELETE_ATTRIBUTE_INIT,
  UPDATE_ATTRIBUTE_INIT,
  ORDER_ATTRIBUTE_INIT, 
  FILTER_ATTRIBUTE_INIT
} from './constants';
import { 
  selectAttributeSuccess,
  getAttributesSuccess,
  getAttributesError, 
  deleteAttributeSuccess,
  deleteAttributeError,
  updateAttributeSuccess,
  updateAttributeError,
  orderAttributesSuccess,
  filterAttributesSuccess
} from './actions';

function* selectAttribute(action) {
  const { value } = action;

  yield put(selectAttributeSuccess(value));
  return;
}

function* getAttributes() {
  const urlAttributes = `${ PATH_API }${GET_ATTRIBUTES_API}`;

  const response = yield call(fetch, urlAttributes, {
      method: 'GET',
  });

  if (response.status >= 200 && response.status < 300) {
    const successResponse = yield call([response, response.json]);

    yield put(getAttributesSuccess(successResponse));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(getAttributesError(errorResponse));
}

function* removeAttribute(action) {
  const { value } = action;
  const { id } = value ? value : {};

  const urlAttributes = `${ PATH_API }${GET_ATTRIBUTES_API}/${id}`;

  const response = yield call(fetch, urlAttributes, {
      method: 'DELETE',
  });

  if (response.status >= 200 && response.status < 300) {
    value.message = "Direccin eliminada exitosamente"; 
    yield put(deleteAttributeSuccess(value));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(deleteAttributeError(errorResponse));
}

function* updateAttribute(action) {
  const { value } = action;
  const { id } = value ? value : {};
  
  const attributeToUpdate = { ...value };

  value.message = "Direccin creada exitosamente"; 
  let attributeId = id ? id : "0";
  let urlAttributes = `${ PATH_API }${GET_ATTRIBUTES_API}`;

  const getResponse = yield call(fetch, `${urlAttributes}/${attributeId}`, {
    method: 'GET'
  });

  const callBody = {
    method: 'POST',
    body: JSON.stringify(attributeToUpdate)
  }

  if (getResponse.status >= 200 && getResponse.status < 300) {
    value.message = "Direccin actualizada exitosamente"; 
    callBody.method = 'PUT';
    urlAttributes = `${urlAttributes}/${id}`
  }

  const response = yield call(fetch, urlAttributes, {
      ...callBody
  });

  if (response.status >= 200 && response.status < 300) {
    yield put(updateAttributeSuccess(value));
    return;
  }
  
  const errorResponse = yield call([response, response.json]);
  value.message = "Hubo un problema creando los cambios"; 
  yield put(updateAttributeError(errorResponse));
}

function* orderAttribute(action) { 
  const { value: {column, items, sense} } = action;

  items.sort((a,b) => {
    if(a[column] < b[column]) {
      return sense;
    }
    if(a[column] > b[column]) {
      return -1*sense;
    }
    return 0;
  })

  const response = {
    attributes: items 
  }

  yield put(orderAttributesSuccess(response));
}

function* filterAttribute(action) { 
  const { value: {fieldValue, items} } = action;
  
  let filtered = items.filter(item => {
    let itemFiltered = false;

    Object.keys(item).map(key => {
      const keyToFilter = item[key] && typeof item[key] !== 'object' ? item[key].toString().toLowerCase() : ""; 
      itemFiltered = itemFiltered || keyToFilter.includes(fieldValue);
      return true;
    })

    return itemFiltered;
  });

  const response = {
    attributes: filtered
  }

  yield put(filterAttributesSuccess(response));
}

export function* watchAttribute() {
  yield takeLatest(GET_ATTRIBUTES, getAttributes);
  yield takeLatest(SELECT_ATTRIBUTE_INIT, selectAttribute);
  yield takeLatest(DELETE_ATTRIBUTE_INIT, removeAttribute);
  yield takeLatest(UPDATE_ATTRIBUTE_INIT, updateAttribute);
  yield takeLatest(ORDER_ATTRIBUTE_INIT, orderAttribute);
  yield takeLatest(FILTER_ATTRIBUTE_INIT, filterAttribute);
}
