import { AUTH0_DOMAIN, AUTH0_CLIENT_ID, AUTH0_AUDIENCE, PATH_BASE } from '../Constants';
import * as Auth0 from "auth0-js";
import utils from './utils';

class LocalAuth0 {
    auth0 = new Auth0.WebAuth({
        domain: AUTH0_DOMAIN,
        clientID: AUTH0_CLIENT_ID,
        redirectUri: PATH_BASE,
        audience: AUTH0_AUDIENCE,
        responseType: "id_token token",
        scope: "openid profile email"
    });

    loginCallback = () => {};
    logoutCallback = () => {};

    userProfile = null;
    authFlag = "isLoggedIn";
    authStatus = this.isAuthenticated // we will create isAuthenticated soon
        ? "init_with_auth_flag"
        : "init_no_auth_flag";
    idToken = null;
    idTokenPayload = null;

    isAuthenticated() {
        return localStorage.getItem(this.authFlag) === "true";
    }

    getUserAttribute(attribute) {
        const userSession = JSON.parse(sessionStorage.getItem('user.session'));
        if (userSession) {
            return userSession[attribute];
        }

        return false;
    }

    getUserRoles () {
        let userRoles = [];
        userRoles.push(sessionStorage.getItem('user.roles'));

        return userRoles;
    }

    hasPermision (roles) {
        const currentRoles = this.getUserRoles();

        if(!roles || !roles.length) {
            return true;
        } else {
            if(currentRoles && currentRoles.length) {
                if(roles && roles.length) {
                    const finalArray = utils.compareArrays(currentRoles, roles);
                    return finalArray && finalArray.length;
                }
            } else {
                return true;
            }
        }

        return false;
    }

    isUserInSession () {
        return JSON.parse(sessionStorage.getItem('user.session')) ? true : false;
    }

    clearSession = () => {        
        localStorage.clear();
        sessionStorage.clear();
        this.userProfile = null;
    }

    localLogout = () => {
        localStorage.clear();
        sessionStorage.clear();
        this.userProfile = null;
        this.logoutCallback({ loggedIn: false });
    }

    logout() {
        this.localLogout();
        this.auth0.logout({
            returnTo: PATH_BASE,
            clientID: AUTH0_CLIENT_ID
        });
    }
}

const auth = new LocalAuth0();
export default auth;