import { 
  SELECT_LOCATION_SUCCESS,
  SELECT_LOCATION_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_LOCATION_SUCCESS,
  UPDATE_LOCATION_ERROR,
  CANCEL_LOCATION_INIT,
  LOCATION_LIST,
  LOCATION_HEADERS,
  GET_LOCATIONS_SUCCESS,
  DELETE_LOCATION_SUCCESS,
  DELETE_LOCATION_ERROR,
  ORDER_LOCATION_SUCCESS,
  FILTER_LOCATION_SUCCESS
} from './constants';
  
  export const initialState = {
    locations: LOCATION_LIST,
    totalLocations: [],
    locationHeaders: LOCATION_HEADERS,
    selectedLocation: false, 
    deleteSuccess: false,
    formLocation: {}
  };
  
  const reducer = (state = initialState, action) => {
    const newState = { ...state };
    const { payload } = action;
    const { locations } = payload ? payload : { locations: false };

    switch (action.type) {
      case GET_LOCATIONS_SUCCESS:
          newState.locations = locations;
          newState.totalLocations = locations;
          
          return {
            ...newState
          };
      case SELECT_LOCATION_INIT:    
        return {
          ...newState
        };

      case SELECT_LOCATION_SUCCESS:  
        newState.selectedLocation = payload;   
        newState.deleteSuccess = false;  
        newState.formLocation = payload; 
        return {
          ...newState
        };
        
      case DELETE_LOCATION_SUCCESS: 
        newState.selectedLocation = false; 
        newState.deleteSuccess = payload; 
        return {
          ...newState
        };

      case UPDATE_FIELD_ATTRIBUTE:
        const { value: { fieldName, fieldValue } } = action;
        let newFormObject = {...newState.selectedLocation};
        newFormObject[fieldName] = fieldValue;
        newState.deleteSuccess = false;
        newState.formLocation = newFormObject;
        newState.selectedLocation = newFormObject;

        return {
          ...newState
        };
      case UPDATE_LOCATION_SUCCESS:
        newState.deleteSuccess = payload;
        newState.formLocation = {};
        newState.selectedLocation = false;

        return {
          ...newState
        };

        case UPDATE_LOCATION_ERROR:
          newState.deleteSuccess = payload;
          newState.formLocation = {};
          newState.selectedLocation = false;
  
          return {
            ...newState
          };
        
        case DELETE_LOCATION_ERROR:
          newState.deleteSuccess = payload;
          newState.formLocation = {};
          newState.selectedLocation = false;
    
          return {
            ...newState
          };  

        case CANCEL_LOCATION_INIT:
          newState.deleteSuccess = false;
          newState.formLocation = {};
          newState.selectedLocation = false;
  
          return {
            ...newState
          };
        case ORDER_LOCATION_SUCCESS:
            newState.deleteSuccess = false;
            newState.formLocation = {};
            newState.selectedLocation = false;
            newState.locations = locations;
            return {
              ...newState
            };
        case FILTER_LOCATION_SUCCESS:
              newState.deleteSuccess = false;
              newState.formLocation = {};
              newState.selectedLocation = false;
              newState.locations = locations;
              return {
                ...newState
              };            
      default:
        return {
          ...newState
        };
    }
  };
  
  export default reducer;
  