import { createStructuredSelector } from 'reselect';

export const getStatus = (state) => state.status;
export const getSelectedStatu = (state) => state.selectedStatu;
export const getDeleteStatu = (state) => state.deleteSuccess;
export const getFormStatu = (state) => state.formStatu;
export const getTotalStatus = (state) => state.totalStatus;

export const attributeSelector = createStructuredSelector({
    status: getStatus,
    selectedStatu: getSelectedStatu,
    deleteSuccess: getDeleteStatu,
    formStatu: getFormStatu
});