import React, { Component } from 'react';
import Typography from '@material-ui/core/Typography';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import { Link } from 'react-router-dom';
import './breadcrumb.css';

export default class CustomBreadcrumb extends Component {
    render() {
        const { route, current } = this.props;

        return (
            <Breadcrumbs aria-label="breadcrumb" className="breadcrumb-wrapper">
                {
                    route.map((item,i) => {
                        return (
                        <Link key={i} to={item.path} className="bread-item">
                            { item.label }    
                        </Link>
                        );
                    })
                }
                <Typography className="current" color="textPrimary">{current}</Typography>
            </Breadcrumbs>
        );
    }
}
