import React, { Component } from "react";
import "./style.css";
import { connect } from "react-redux";
import CustomForm from "../../components/customform/customform";
import CustomTable from "../../components/table/table";
import { getStatus, getSelectedStatu, getDeleteStatu, getFormStatu, getTotalStatus } from "./selectors";
import { Spinner, Alert } from 'react-bootstrap';
import { 
  getStatuActions,
  getStatuButton,
  GET_STATUS,
  SELECT_STATU_INIT, 
  STATU_FORMS, 
  DELETE_STATU_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_STATU_INIT,
  CANCEL_STATU_INIT,
  ORDER_STATU_INIT,
  FILTER_STATU_INIT,
} from './constants'


class ComponentStatu extends Component {

  componentWillMount () {
    const { getStatus } = this.props;
    getStatus();
  }

  componentWillReceiveProps (nextProps) {
    const { getStatus } = nextProps;

    if (nextProps.deleteStatu && this.props.deleteStatu !== nextProps.deleteStatu) {
      getStatus();
    }
  }

  render() {
    const {
      statuHeaders, 
      status,
      selectStatu,
      selectedStatu,
      removeStatu,
      deleteStatu,
      updateStatus,
      updatedStatu,
      updateStatu,
      cancelStatu,
      orderStatu,
      filterStatu,
      totalStatus
  } = this.props;

    return (
      <div className="App">
        { deleteStatu ?
          <Alert  variant="success">
            {deleteStatu.message}
          </Alert>
          : ''
        }

        {!selectedStatu ? 
          <div className='statu-list_wrapper'>
          { status && !status.emprty ? 
            <CustomTable 
              onSelectRow={selectStatu} 
              className="status-table" 
              items={status}
              totalItems={totalStatus}
              orderTable={orderStatu}
              headers={statuHeaders}
              filterTable = {filterStatu}
              paginator = {true}
              buttons={getStatuButton(() => selectStatu({}))} 
            /> :
            <Spinner className="spinner-wrapper" animation="border" role="status">
              <span className="sr-only">Loading...</span>
            </Spinner>
          }
          
        </div> :  
        <div className='statu-form_wrapper'>
          <CustomForm 
            formName="customStatu" 
            values={ selectedStatu } 
            items={ STATU_FORMS } 
            updateAction = { updateStatus }
            buttons={
              getStatuActions(
                () => updateStatu(updatedStatu), 
                () => cancelStatu(), 
                () => removeStatu(selectedStatu)
            )} 
            />
        </div>
        }
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    statuHeaders: state.statuHeaders,
    status: getStatus(state),
    selectedStatu: getSelectedStatu(state),
    deleteStatu: getDeleteStatu(state),
    updatedStatu: getFormStatu(state),
    totalStatus: getTotalStatus(state),
  };
};

const mapDispachToProps = dispatch => {
  return { 
    getStatus: () => dispatch({ type: GET_STATUS, value: 1 }),
    updateStatus: (value) => dispatch({ type: UPDATE_FIELD_ATTRIBUTE, value }),
    selectStatu: (statu) => dispatch({ type: SELECT_STATU_INIT, value: statu }),
    removeStatu: (statu) => dispatch({ type: DELETE_STATU_INIT, value: statu }),
    updateStatu: (statu) => dispatch({ type: UPDATE_STATU_INIT, value: statu }),
    cancelStatu: () => dispatch({ type: CANCEL_STATU_INIT}),
    orderStatu: (prop) => dispatch({ type: ORDER_STATU_INIT, value: prop }),
    filterStatu: (prop) => dispatch({ type: FILTER_STATU_INIT, value: prop }),
  };
};

export default connect(
  mapStateToProps,
  mapDispachToProps
)(ComponentStatu);