import { createStructuredSelector } from 'reselect';

export const getWorkers = (state) => state.workers;
export const getSelectedWorker = (state) => state.selectedWorker;
export const getDeleteWorker = (state) => state.deleteSuccess;
export const getFormWorker = (state) => state.formWorker;
export const getTotalWorkers = (state) => state.totalWorkers;

export const attributeSelector = createStructuredSelector({
    workers: getWorkers,
    selectedWorker: getSelectedWorker,
    deleteSuccess: getDeleteWorker,
    formWorker: getFormWorker
});