import React from 'react';
import Container from 'react-bootstrap/Container';
import "./home.css";
import { ITEMS } from "./constants";
import CustomCard from '../../components/customcard/customcard';
import { useAuth0 } from "../../controllers/react-auth0-spa";
import CustomNavBar from '../../components/navbar/navbar';
import Spinner from 'react-bootstrap/Spinner';
import auth from '../../controllers/session';

const Home = () => {
    const { isAuthenticated, loginWithRedirect, loading  } = useAuth0();

    if(!isAuthenticated) {
        auth.clearSession();
    }

    if (loading ) {
        return <Spinner className="spinner-wrapper" animation="border" role="status">
                    <span className="sr-only">Loading...</span>
                </Spinner>;
    }
    return (
        <div>
        {!isAuthenticated && (
            <Container className='main-wrapper home'>
                <div className='login-wrapper'> 
                    <CustomCard items={ITEMS} onItemClick={loginWithRedirect}/>
                </div>
            </Container>
        )}

        {isAuthenticated ?
            <CustomNavBar />
            : false
        }
        </div>
    );
}

export default Home;