import { 
  SELECT_ZONE_SUCCESS,
  SELECT_ZONE_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_ZONE_SUCCESS,
  UPDATE_ZONE_ERROR,
  CANCEL_ZONE_INIT,
  ZONE_LIST,
  ZONE_HEADERS,
  GET_ZONES_SUCCESS,
  DELETE_ZONE_SUCCESS,
  DELETE_ZONE_ERROR,
  ORDER_ZONE_SUCCESS,
  FILTER_ZONE_SUCCESS,
} from './constants';
  
  export const initialState = {
    zones: ZONE_LIST,
    zoneHeaders: ZONE_HEADERS,
    selectedZone: false, 
    deleteSuccess: false,
    totalZones: [],
    formZone: {}
  };
  
  const reducer = (state = initialState, action) => {
    const newState = { ...state };
    const { payload } = action;
    const { zones } = payload ? payload : { zones: false };

    switch (action.type) {
      case GET_ZONES_SUCCESS:
          newState.zones = zones;
          newState.totalZones = zones;

          return {
            ...newState
          };
      case SELECT_ZONE_INIT:    
        return {
          ...newState
        };

      case SELECT_ZONE_SUCCESS:  
        newState.selectedZone = payload;   
        newState.deleteSuccess = false;  
        newState.formZone = payload; 
        return {
          ...newState
        };
        
      case DELETE_ZONE_SUCCESS: 
        newState.selectedZone = false; 
        newState.deleteSuccess = payload; 
        return {
          ...newState
        };

      case UPDATE_FIELD_ATTRIBUTE:
        const { value: { fieldName, fieldValue } } = action;
        let newFormObject = {...newState.selectedZone};
        newFormObject[fieldName] = fieldValue;
        newState.deleteSuccess = false;
        newState.formZone = newFormObject;
        newState.selectedZone = newFormObject;

        return {
          ...newState
        };
      case UPDATE_ZONE_SUCCESS:
        newState.deleteSuccess = payload;
        newState.formZone = {};
        newState.selectedZone = false;

        return {
          ...newState
        };

        case UPDATE_ZONE_ERROR:
          newState.deleteSuccess = payload;
          newState.formZone = {};
          newState.selectedZone = false;
  
          return {
            ...newState
          };
        
        case DELETE_ZONE_ERROR:
          newState.deleteSuccess = payload;
          newState.formZone = {};
          newState.selectedZone = false;
    
          return {
            ...newState
          };  

        case CANCEL_ZONE_INIT:
          newState.deleteSuccess = false;
          newState.formZone = {};
          newState.selectedZone = false;
  
          return {
            ...newState
          };
      case ORDER_ZONE_SUCCESS:
            newState.deleteSuccess = false;
            newState.formZone = {};
            newState.selectedZone = false;
            newState.zones = zones;
            return {
              ...newState
            };
      case FILTER_ZONE_SUCCESS:
              newState.deleteSuccess = false;
              newState.formZone = {};
              newState.selectedZone = false;
              newState.zones = zones;
              return {
                ...newState
              };
      default:
        return {
          ...newState
        };
    }
  };
  
  export default reducer;
  