import { createStructuredSelector } from 'reselect';

export const getAddresses = (state) => state.addresses;
export const getSelectedAddress = (state) => state.selectedAddress;
export const getDeleteAddress = (state) => state.deleteSuccess;
export const getFormAddress = (state) => state.formAddress;

export const addressSelector = createStructuredSelector({
    addresses: getAddresses,
    selectedAddress: getSelectedAddress,
    deleteSuccess: getDeleteAddress,
    formAddress: getFormAddress
});