import { 
  SELECT_STATU_SUCCESS,
  SELECT_STATU_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_STATU_SUCCESS,
  UPDATE_STATU_ERROR,
  CANCEL_STATU_INIT,
  STATU_LIST,
  STATU_HEADERS,
  GET_STATUS_SUCCESS,
  DELETE_STATU_SUCCESS,
  DELETE_STATU_ERROR,
  ORDER_STATU_SUCCESS,
  FILTER_STATU_SUCCESS
} from './constants';
  
  export const initialState = {
    status: STATU_LIST,
    totalStatus: [],
    statuHeaders: STATU_HEADERS,
    selectedStatu: false, 
    deleteSuccess: false,
    formStatu: {}
  };
  
  const reducer = (state = initialState, action) => {
    const newState = { ...state };
    const { payload } = action;
    const { statuses } = payload ? payload : { statuses: false };

    switch (action.type) {
      case GET_STATUS_SUCCESS:
          newState.status = statuses;
          newState.totalStatus = statuses;
          
          return {
            ...newState
          };
      case SELECT_STATU_INIT:    
        return {
          ...newState
        };

      case SELECT_STATU_SUCCESS:  
        newState.selectedStatu = payload;   
        newState.deleteSuccess = false;  
        newState.formStatu = payload; 
        return {
          ...newState
        };
        
      case DELETE_STATU_SUCCESS: 
        newState.selectedStatu = false; 
        newState.deleteSuccess = payload; 
        return {
          ...newState
        };

      case UPDATE_FIELD_ATTRIBUTE:
        const { value: { fieldName, fieldValue } } = action;
        let newFormObject = {...newState.selectedStatu};
        newFormObject[fieldName] = fieldValue;
        newState.deleteSuccess = false;
        newState.formStatu = newFormObject;
        newState.selectedStatu = newFormObject;

        return {
          ...newState
        };
      case UPDATE_STATU_SUCCESS:
        newState.deleteSuccess = payload;
        newState.formStatu = {};
        newState.selectedStatu = false;

        return {
          ...newState
        };

        case UPDATE_STATU_ERROR:
          newState.deleteSuccess = payload;
          newState.formStatu = {};
          newState.selectedStatu = false;
  
          return {
            ...newState
          };
        
        case DELETE_STATU_ERROR:
          newState.deleteSuccess = payload;
          newState.formStatu = {};
          newState.selectedStatu = false;
    
          return {
            ...newState
          };  

        case CANCEL_STATU_INIT:
          newState.deleteSuccess = false;
          newState.formStatu = {};
          newState.selectedStatu = false;
  
          return {
            ...newState
          };
        case ORDER_STATU_SUCCESS:
            newState.deleteSuccess = false;
            newState.formStatu = {};
            newState.selectedStatu = false;
            newState.status = statuses;
            return {
              ...newState
            };
        case FILTER_STATU_SUCCESS:
              newState.deleteSuccess = false;
              newState.formStatu = {};
              newState.selectedStatu = false;
              newState.status = statuses;
              return {
                ...newState
              };            
      default:
        return {
          ...newState
        };
    }
  };
  
  export default reducer;
  