import { createStructuredSelector } from 'reselect';

export const getLocations = (state) => state.locations;
export const getSelectedLocation = (state) => state.selectedLocation;
export const getDeleteLocation = (state) => state.deleteSuccess;
export const getFormLocation = (state) => state.formLocation;
export const getTotalLocations = (state) => state.totalLocations;

export const locationSelector = createStructuredSelector({
    locations: getLocations,
    selectedLocation: getSelectedLocation,
    deleteSuccess: getDeleteLocation,
    formLocation: getFormLocation
});