import { 
  SELECT_ATTRIBUTE_SUCCESS,
  SELECT_ATTRIBUTE_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_ATTRIBUTE_SUCCESS,
  UPDATE_ATTRIBUTE_ERROR,
  CANCEL_ATTRIBUTE_INIT,
  ATTRIBUTE_LIST,
  ATTRIBUTE_HEADERS,
  GET_ATTRIBUTES_SUCCESS,
  DELETE_ATTRIBUTE_SUCCESS,
  DELETE_ATTRIBUTE_ERROR,
  ORDER_ATTRIBUTE_SUCCESS,
  FILTER_ATTRIBUTE_SUCCESS
} from './constants';
  
  export const initialState = {
    attributes: ATTRIBUTE_LIST,
    totalAttributes: [],
    attributeHeaders: ATTRIBUTE_HEADERS,
    selectedAttribute: false, 
    deleteSuccess: false,
    formAttribute: {}
  };
  
  const reducer = (state = initialState, action) => {
    const newState = { ...state };
    const { payload } = action;
    const { attributes } = payload ? payload : { attributes: false };

    switch (action.type) {
      case GET_ATTRIBUTES_SUCCESS:
          newState.attributes = attributes;
          newState.totalAttributes = attributes;
          
          return {
            ...newState
          };
      case SELECT_ATTRIBUTE_INIT:    
        return {
          ...newState
        };

      case SELECT_ATTRIBUTE_SUCCESS:  
        newState.selectedAttribute = payload;   
        newState.deleteSuccess = false;  
        newState.formAttribute = payload; 
        return {
          ...newState
        };
        
      case DELETE_ATTRIBUTE_SUCCESS: 
        newState.selectedAttribute = false; 
        newState.deleteSuccess = payload; 
        return {
          ...newState
        };

      case UPDATE_FIELD_ATTRIBUTE:
        const { value: { fieldName, fieldValue } } = action;
        let newFormObject = {...newState.selectedAttribute};
        newFormObject[fieldName] = fieldValue;
        newState.deleteSuccess = false;
        newState.formAttribute = newFormObject;
        newState.selectedAttribute = newFormObject;

        return {
          ...newState
        };
      case UPDATE_ATTRIBUTE_SUCCESS:
        newState.deleteSuccess = payload;
        newState.formAttribute = {};
        newState.selectedAttribute = false;

        return {
          ...newState
        };

        case UPDATE_ATTRIBUTE_ERROR:
          newState.deleteSuccess = payload;
          newState.formAttribute = {};
          newState.selectedAttribute = false;
  
          return {
            ...newState
          };
        
        case DELETE_ATTRIBUTE_ERROR:
          newState.deleteSuccess = payload;
          newState.formAttribute = {};
          newState.selectedAttribute = false;
    
          return {
            ...newState
          };  

        case CANCEL_ATTRIBUTE_INIT:
          newState.deleteSuccess = false;
          newState.formAttribute = {};
          newState.selectedAttribute = false;
  
          return {
            ...newState
          };
        case ORDER_ATTRIBUTE_SUCCESS:
            newState.deleteSuccess = false;
            newState.formAttribute = {};
            newState.selectedAttribute = false;
            newState.attributes = attributes;
            return {
              ...newState
            };
        case FILTER_ATTRIBUTE_SUCCESS:
              newState.deleteSuccess = false;
              newState.formAttribute = {};
              newState.selectedAttribute = false;
              newState.attributes = attributes;
              return {
                ...newState
              };            
      default:
        return {
          ...newState
        };
    }
  };
  
  export default reducer;
  