import { 
  SELECT_ADDRESS_SUCCESS,
  SELECT_ADDRESS_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_ADDRESS_SUCCESS,
  UPDATE_ADDRESS_ERROR,
  CANCEL_ADDRESS_INIT,
  ADDRESS_LIST,
  ADDRESS_HEADERS,
  GET_ADDRESSES_SUCCESS,
  DELETE_ADDRESS_SUCCESS,
  DELETE_ADDRESS_ERROR
} from './constants';
  
  export const initialState = {
    addresses: ADDRESS_LIST,
    addressHeaders: ADDRESS_HEADERS,
    selectedAddress: false, 
    deleteSuccess: false,
    formAddress: {}
  };
  
  const reducer = (state = initialState, action) => {
    const newState = { ...state };
    const { payload } = action;

    switch (action.type) {
      case GET_ADDRESSES_SUCCESS:
          const { payload: { addresses } } = action;
          newState.addresses = addresses;

          return {
            ...newState
          };
      case SELECT_ADDRESS_INIT:    
        return {
          ...newState
        };

      case SELECT_ADDRESS_SUCCESS:  
        newState.selectedAddress = payload;   
        newState.deleteSuccess = false;  
        newState.formAddress = payload; 
        return {
          ...newState
        };
        
      case DELETE_ADDRESS_SUCCESS: 
        newState.selectedAddress = false; 
        newState.deleteSuccess = payload; 
        return {
          ...newState
        };

      case UPDATE_FIELD_ATTRIBUTE:
        const { value: { fieldName, fieldValue } } = action;
        let newFormObject = {...newState.selectedAddress};
        newFormObject[fieldName] = fieldValue;
        newState.deleteSuccess = false;
        newState.formAddress = newFormObject;
        newState.selectedAddress = newFormObject;

        return {
          ...newState
        };
      case UPDATE_ADDRESS_SUCCESS:
        newState.deleteSuccess = payload;
        newState.formAddress = {};
        newState.selectedAddress = false;

        return {
          ...newState
        };

        case UPDATE_ADDRESS_ERROR:
          newState.deleteSuccess = payload;
          newState.formAddress = {};
          newState.selectedAddress = false;
  
          return {
            ...newState
          };
        
        case DELETE_ADDRESS_ERROR:
          newState.deleteSuccess = payload;
          newState.formAddress = {};
          newState.selectedAddress = false;
    
          return {
            ...newState
          };  

        case CANCEL_ADDRESS_INIT:
          newState.deleteSuccess = false;
          newState.formAddress = {};
          newState.selectedAddress = false;
  
          return {
            ...newState
          };
      default:
        return {
          ...newState
        };
    }
  };
  
  export default reducer;
  