export const getUserItem = (name, picture) => {
    const ITEM_PROFILE = [
        {
            
            position: 'left',
            path: '/profile',
            name: name,
            css: faUserCircle,
            key: 6,
            label: name,
            className:'',
            active: false,
            picture: picture
        }
    ]

    return ITEM_PROFILE;
}