import { createStructuredSelector } from 'reselect';

export const getBases = (state) => state.bases;
export const getSelectedBase = (state) => state.selectedBase;
export const getDeleteBase = (state) => state.deleteSuccess;
export const getFormBase = (state) => state.formBase;
export const getTotalBases = (state) => state.totalBases;

export const addressSelector = createStructuredSelector({
    bases: getBases,
    selectedBase: getSelectedBase,
    deleteSuccess: getDeleteBase,
    formBase: getFormBase
});