import { 
    GET_ZONE_SUCCESS,
    ZONES_HEADERS
} from './constants';
    
    export const initialState = {
        zones: false,
        zoneHeaders: ZONES_HEADERS
    };
    
    const reducer = (state = initialState, action) => {
        const newState = { ...state };
        const { payload } = action;
    
        switch (action.type) {
            case GET_ZONE_SUCCESS:
            const { payload: { zones } } = action;
            newState.zones = zones;

            return {
                ...newState
            };
            default:
            return {
                ...newState
            };
        }
    };
    
    export default reducer;
    