import { takeLatest, put, call } from "redux-saga/effects";
import { GET_ZONES_API, GET_LOCATIONS_API, PATH_API } from '../../Constants';
import { 
  GET_ZONES,
  SELECT_ZONE_INIT,
  DELETE_ZONE_INIT,
  UPDATE_ZONE_INIT,
  FILTER_ZONE_INIT,
  ORDER_ZONE_INIT
} from './constants';
import { 
  selectZoneSuccess,
  getZonesSuccess,
  getZonesError, 
  deleteZoneSuccess,
  deleteZoneError,
  updateZoneSuccess,
  updateZoneError,
  filterZonesSuccess,
  orderZonesSuccess
} from './actions';

function* selectZone(action) {
  const { value } = action;
  
  yield put(selectZoneSuccess(value));
  return;
}

function* getZones() {
  const urlZones = `${ PATH_API }${GET_ZONES_API}`;

  const response = yield call(fetch, urlZones, {
      method: 'GET',
  });

  if (response.status >= 200 && response.status < 300) {
    const successResponse = yield call([response, response.json]);
    yield put(getZonesSuccess(successResponse));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(getZonesError(errorResponse));
}

function* removeZone(action) {
  const { value } = action;
  const { id } = value ? value : {};

  const urlZones = `${ PATH_API }${GET_ZONES_API}/${id}`;

  const response = yield call(fetch, urlZones, {
      method: 'DELETE',
  });

  if (response.status >= 200 && response.status < 300) {
    value.message = "Zona eliminada exitosamente"; 
    yield put(deleteZoneSuccess(value));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(deleteZoneError(errorResponse));
}

function* updateZone(action) {
  const { value } = action;
  const { id, route_time } = value ? value : {};

  const zoneToUpdate = {
    ...value
  }

  value.message = "Zona creada exitosamente"; 
  let zoneId = id ? id : "0";
  let zoneTime = route_time ? parseInt(route_time) : 0;
  let urlZones = `${ PATH_API }${GET_ZONES_API}`;

  zoneToUpdate.route_time = zoneTime;

  const getResponse = yield call(fetch, `${urlZones}/${zoneId}`, {
    method: 'GET'
  });

  const callBody = {
    method: 'POST',
    body: JSON.stringify(zoneToUpdate)
  }

  if (getResponse.status >= 200 && getResponse.status < 300) {
    value.message = "Direccin actualizada exitosamente"; 
    callBody.method = 'PUT';
    urlZones = `${urlZones}/${id}`
  } else {
    zoneToUpdate.sectors_ids = [];
  }

  const response = yield call(fetch, urlZones, {
      ...callBody
  });

  if (response.status >= 200 && response.status < 300) {
    yield put(updateZoneSuccess(value));
    return;
  }
  
  const errorResponse = yield call([response, response.json]);
  value.message = "Hubo un problema creando los cambios"; 
  yield put(updateZoneError(errorResponse));
}

function* orderZone(action) { 
  const { value: {column, items, sense} } = action;

  items.sort((a,b) => {
    if(a[column] < b[column]) {
      return sense;
    }
    if(a[column] > b[column]) {
      return -1*sense;
    }
    return 0;
  })

  const response = {
    zones: items 
  }

  yield put(orderZonesSuccess(response));
}

function* filterZone(action) { 
  const { value: {fieldValue, items} } = action;
  
  let filtered = items.filter(item => {
    let itemFiltered = false;

    Object.keys(item).map(key => {
      const keyToFilter = item[key] && typeof item[key] !== 'object' ? item[key].toString().toLowerCase() : ""; 
      itemFiltered = itemFiltered || keyToFilter.includes(fieldValue);
      return true;
    })

    return itemFiltered;
  });

  const response = {
    zones: filtered
  }

  yield put(filterZonesSuccess(response));
}

export function* watchZone() {
  yield takeLatest(GET_ZONES, getZones);
  yield takeLatest(SELECT_ZONE_INIT, selectZone);
  yield takeLatest(DELETE_ZONE_INIT, removeZone);
  yield takeLatest(UPDATE_ZONE_INIT, updateZone);
  yield takeLatest(ORDER_ZONE_INIT, orderZone);
  yield takeLatest(FILTER_ZONE_INIT, filterZone);
}
