import React, { Component } from "react";
import "./navbar.css";
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import User from "../../navegation/sector/index";
import NoMatch from "../../navegation/nomatch";
import About from  "../../navegation/about";
import SideBar from "../sidebar/sidebar";
import Home from "../../navegation/home";
import Profile from "../../navegation/profile";
import Search from "../../navegation/search";
import BasesPage from "../../navegation/bases";
import AddressPage from "../../navegation/addresses";
import SectorsPage from "../../navegation/sector";
import ZonePage from "../../navegation/zones";
import TypePage from "../../navegation/types";
import LocationPage from "../../navegation/locations";
import AttributePage from "../../navegation/attributes";
import WorkerPage from "../../navegation/workers";
import StatuPage from "../../navegation/status";

class CustomNavBar extends Component {
  render() {
    return (
      <Router>
            <SideBar></SideBar>

            <Switch>
              <Route exact path="/" component={About} />
              <Route path="/user" component={User} />
              <Route path="/home" component={Home} />
              <Route path="/profile" component={Profile} />
              <Route path="/sectors" component={SectorsPage} />
              <Route path="/search" component={Search} />
              <Route path="/bases" component={BasesPage} />
              <Route path="/addresses" component={AddressPage} />
              <Route path="/zones" component={ZonePage} />
              <Route path="/types" component={TypePage} />
              <Route path="/locations" component={LocationPage} />
              <Route path="/attributes" component={AttributePage} />
              <Route path="/workers" component={WorkerPage} />
              <Route path="/status" component={StatuPage} />

              <Route component={NoMatch} />
            </Switch>
      </Router>
    );
  }
}

export default CustomNavBar;