import { createStructuredSelector } from 'reselect';

export const getAttributes = (state) => state.attributes;
export const getSelectedAttribute = (state) => state.selectedAttribute;
export const getDeleteAttribute = (state) => state.deleteSuccess;
export const getFormAttribute = (state) => state.formAttribute;
export const getTotalAttributes = (state) => state.totalAttributes;

export const attributeSelector = createStructuredSelector({
    attributes: getAttributes,
    selectedAttribute: getSelectedAttribute,
    deleteSuccess: getDeleteAttribute,
    formAttribute: getFormAttribute
});