import React, { Component } from 'react';
import { Card, Accordion } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import './customcard.css';
import { Image, Button } from 'react-bootstrap';
import auth from '../../controllers/session';

export default class CustomCard extends Component {
    handleClick = () => {
        const { path, onItemClick } = this.props;
        onItemClick(path);
    }

    render() {
        const { items, onItemClick } = this.props;

        return (
                <Accordion className='cards-wrapper' defaultActiveKey="0">
                {
                items.map((item) => {
                    const { title, description, path, icon, buttonLabel, key, type, role, picture, accordion } = item;   
                    const found = auth.hasPermision(role);

                    if (found) {
                        return (
                            <Card key={key}>                                
                                <Card.Header>
                                    { accordion ? 
                                    <Accordion.Toggle as={Button} variant="link" eventKey={key}> 
                                        {   picture ?
                                            <Image className='card-image' src={ picture } roundedCircle /> :
                                            icon ? <FontAwesomeIcon className='card-icon' icon={icon} /> : false 
                                        }
                                        <Card.Title>{title}</Card.Title>
                                    </Accordion.Toggle>
                                    :   <div className="header-wrapper">
                                            {   picture ?
                                                <Image className='card-image' src={ picture } roundedCircle /> 
                                                : 
                                                icon ? <FontAwesomeIcon className='card-icon' icon={icon} /> : false 
                                            }
                                            <Card.Title>{title}</Card.Title>
                                        </div>
                                }
                                </Card.Header>
                                { accordion ?
                                    <Accordion.Collapse eventKey={key}>
                                        <Card.Body>
                                            <Card.Text> 
                                                {description}
                                            </Card.Text>
                                            { type && type === 'button' ? 
                                                <Button className='btn btn-primary ' onClick={onItemClick}>
                                                    {buttonLabel}
                                                </Button>
                                                : 
                                                <Link to={path} className='sidebar-nav-link btn btn-primary '>
                                                    {buttonLabel}
                                                </Link>
                                            }
                                        </Card.Body>
                                    </Accordion.Collapse> : 
                                    <Card.Body>
                                        <Card.Text> 
                                            {description}
                                        </Card.Text>
                                        { type && type === 'button' ? 
                                            <Button className='btn btn-primary ' onClick={onItemClick}>
                                                {buttonLabel}
                                            </Button>
                                            : 
                                            <Link to={path} className='sidebar-nav-link btn btn-primary '>
                                                {buttonLabel}
                                            </Link>
                                        }
                                    </Card.Body>
                                }

                            </Card>
                        )
                    }

                    return false;
                })
            }
            </Accordion>
        );
    }
}
