import React, { Component } from "react";
import "./style.css";
import { connect } from "react-redux";
import CustomForm from "../../components/customform/customform";
import CustomTable from "../../components/table/table";
import { getLocations, getSelectedLocation, getDeleteLocation, getFormLocation, getTotalLocations } from "./selectors";
import { Spinner, Alert } from 'react-bootstrap';
import { 
  getLocationActions,
  getLocationButton,
  GET_LOCATIONS,
  SELECT_LOCATION_INIT, 
  LOCATION_FORMS, 
  DELETE_LOCATION_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_LOCATION_INIT,
  CANCEL_LOCATION_INIT,
  ORDER_LOCATION_INIT,
  FILTER_LOCATION_INIT,
} from './constants'


class ComponentLocation extends Component {

  componentWillMount () {
    const { getLocations } = this.props;
    getLocations();
  }

  componentWillReceiveProps (nextProps) {
    const { getLocations } = nextProps;

    if (nextProps.deleteLocation && this.props.deleteLocation !== nextProps.deleteLocation) {
      getLocations();
    }
  }

  render() {
    const {
      locationHeaders, 
      locations,
      selectLocation,
      selectedLocation,
      removeLocation,
      deleteLocation,
      updateAttributes,
      updatedLocation,
      updateLocation,
      cancelLocation,
      orderLocation,
      filterLocation,
      totalLocations
  } = this.props;

    return (
      <div className="App">
        { deleteLocation ?
          <Alert  variant="success">
            {deleteLocation.message}
          </Alert>
          : ''
        }

        {!selectedLocation ? 
          <div className='location-list_wrapper'>
          { locations && !locations.emprty ? 
            <CustomTable 
              onSelectRow={selectLocation} 
              className="locations-table" 
              items={locations}
              totalItems={totalLocations}
              orderTable={orderLocation}
              headers={locationHeaders}
              filterTable = {filterLocation}
              paginator = {true}
              buttons={getLocationButton(() => selectLocation({}))} 
            /> :
            <Spinner className="spinner-wrapper" animation="border" role="status">
              <span className="sr-only">Loading...</span>
            </Spinner>
          }
          
        </div> :  
        <div className='location-form_wrapper'>
          <CustomForm 
            formName="customLocation" 
            values={ selectedLocation } 
            items={ LOCATION_FORMS } 
            updateAction = { updateAttributes }
            buttons={
              getLocationActions(
                () => updateLocation(updatedLocation), 
                () => cancelLocation(), 
                () => removeLocation(selectedLocation)
            )} 
            />
        </div>
        }
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    locationHeaders: state.locationHeaders,
    locations: getLocations(state),
    selectedLocation: getSelectedLocation(state),
    deleteLocation: getDeleteLocation(state),
    updatedLocation: getFormLocation(state),
    totalLocations: getTotalLocations(state),
  };
};

const mapDispachToProps = dispatch => {
  return { 
    getLocations: () => dispatch({ type: GET_LOCATIONS, value: 1 }),
    updateAttributes: (value) => dispatch({ type: UPDATE_FIELD_ATTRIBUTE, value }),
    selectLocation: (location) => dispatch({ type: SELECT_LOCATION_INIT, value: location }),
    removeLocation: (location) => dispatch({ type: DELETE_LOCATION_INIT, value: location }),
    updateLocation: (location) => dispatch({ type: UPDATE_LOCATION_INIT, value: location }),
    cancelLocation: () => dispatch({ type: CANCEL_LOCATION_INIT}),
    orderLocation: (prop) => dispatch({ type: ORDER_LOCATION_INIT, value: prop }),
    filterLocation: (prop) => dispatch({ type: FILTER_LOCATION_INIT, value: prop }),
  };
};

export default connect(
  mapStateToProps,
  mapDispachToProps
)(ComponentLocation);