export const getBaseButton = (handlerSafe) => {
    return [
        {
            label: "Nueva Base",
            className: "",
            action: false,
            style: "primary",
            onClick: handlerSafe
        }
    ]
}

export const getBaseActions = (handlerSave, handlerClean, handlerRemove) => {
    return [
        {
            label: "Guardar Cambios",
            className: "",
            action: false,
            style: "primary",
            variant: "outlined",
            onClick: handlerSave
        },
        {
            label: "Eliminar",
            className: "",
            action: false,
            style: "default",
            variant: "outlined",
            onClick: handlerRemove
        },
        {
            label: "Cancelar",
            className: "",
            action: false,
            style: "secondary",
            variant: "outlined",
            onClick: handlerClean
        },
    ];
}

