import React, { Component } from 'react';
import { Pagination, Container } from 'react-bootstrap';
import "./search.css";
import { SEARCH_FORM, SEARCH_RESULT } from "./constants";
import CustomCard from '../../components/customcard/customcard';
import CustomField from '../../components/customform/customfield';


export default class componentName extends Component {
    render() {
        return (
            <Container className='main-wrapper search'>
                <h1> Bsqueda de sectores </h1>
                <div className="search-content_wrapper">
                    <div className="search-content_input">
                        <CustomField {...SEARCH_FORM}/>
                    </div>
                    <h2> Resultados </h2>
                    <div className="search-content_results">
                        <CustomCard items={ SEARCH_RESULT }/>
                    </div>
                    <div className="search-content_paginator">
                        <Pagination>
                            <Pagination.First />
                            <Pagination.Prev />
                            <Pagination.Item>{1}</Pagination.Item>
                            <Pagination.Ellipsis />
                        
                            <Pagination.Item>{10}</Pagination.Item>
                            <Pagination.Item>{11}</Pagination.Item>
                            <Pagination.Item active>{12}</Pagination.Item>
                            <Pagination.Item>{13}</Pagination.Item>
                            <Pagination.Item disabled>{14}</Pagination.Item>
                        
                            <Pagination.Ellipsis />
                            <Pagination.Item>{20}</Pagination.Item>
                            <Pagination.Next />
                            <Pagination.Last />
                        </Pagination>
                    </div>
                </div>
            </Container>
        );
    }
}
