import React, { Component } from "react";
import "./sector.css";
import { connect } from "react-redux";
import CustomForm from "../../components/customform/customform";
import CustomTable from "../../components/table/table";
import { getSectors, getSelectedSector, getDeleteSector, getFormSector, getTotalSectors } from "./selectors";
import { Spinner, Alert } from 'react-bootstrap';
import { 
  getSectorActions,
  getSectorButton,
  SELECT_SECTOR_INIT, 
  SECTOR_FORMS,
  DELETE_SECTOR_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_SECTOR_INIT,
  CANCEL_SECTOR_INIT,
  FILTER_SECTOR_INIT,
} from './constants'


class ComponentSector extends Component {

  componentWillMount () {
    const { getSectors } = this.props;
    getSectors();
  }

  componentWillReceiveProps (nextProps) {
    const { getSectors } = nextProps;

    if (nextProps.deleteSector && this.props.deleteSector !== nextProps.deleteSector) {
      getSectors();
    }
  }

  render() {
    const {
      userHeaders, 
      sectors,
      selectSector,
      selectedSector,
      removeSector,
      deleteSector,
      updateAttributes,
      updatedSector,
      updateSector,
      cancelSector,
      totalSectors,
      filterSectors
  } = this.props;

    return (
      <div className="App">
        { deleteSector ?
          <Alert  variant="success">
            {deleteSector.message}
          </Alert>
          : ''
        }

        {!selectedSector ? 
          <div className='sector-list_wrapper'>
          { sectors && !sectors.emprty ? 
            <CustomTable 
              onSelectRow={selectSector} 
              className="sectors-table" 
              items={sectors} 
              totalItems={totalSectors}
              headers={userHeaders}
              filterTable = {filterSectors}
              buttons={getSectorButton(() => selectSector({}))} 
            /> :
            <Spinner className="spinner-wrapper" animation="border" role="status">
              <span className="sr-only">Loading...</span>
            </Spinner>
          }
          
        </div> :  
        <div className='user-form_wrapper'>
          <CustomForm 
            formName="customSector" 
            values={ selectedSector } 
            items={ SECTOR_FORMS } 
            updateAction = { updateAttributes }
            buttons={
              getSectorActions(
                () => updateSector(updatedSector), 
                () => cancelSector(), 
                () => removeSector(selectedSector)
            )} 
            />
        </div>
        }
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    age: state.age,
    email: state.email,
    users: state.users,
    userHeaders: state.userHeaders,
    sectors: getSectors(state),
    selectedSector: getSelectedSector(state),
    deleteSector: getDeleteSector(state),
    updatedSector: getFormSector(state),
    totalSectors: getTotalSectors(state),
  };
};

const mapDispachToProps = dispatch => {
  return { 
    onAgeUp: () => dispatch({ type: "AGE_UP", value: 1 }),
    onAgeDown: () => dispatch({ type: "AGE_DOWN", value: 1 }),
    getUser: () => dispatch({ type: "GET_USER", value: 1 }),
    getSectors: () => dispatch({ type: "GET_SECTORS", value: 1 }),
    updateAttributes: (value) => dispatch({ type: UPDATE_FIELD_ATTRIBUTE, value }),
    selectSector: (sector) => dispatch({ type: SELECT_SECTOR_INIT, value: sector }),
    removeSector: (sector) => dispatch({ type: DELETE_SECTOR_INIT, value: sector }),
    updateSector: (sector) => dispatch({ type: UPDATE_SECTOR_INIT, value: sector }),
    cancelSector: () => dispatch({ type: CANCEL_SECTOR_INIT}),
    filterSectors: (prop) => dispatch({ type: FILTER_SECTOR_INIT, value: prop }),
  };
};

export default connect(
  mapStateToProps,
  mapDispachToProps
)(ComponentSector);