import { takeLatest, put, call } from "redux-saga/effects";
import { GET_SECTORS_API, PATH_API } from '../../Constants';
import { 
  SELECT_SECTOR_INIT,
  DELETE_SECTOR_INIT,
  UPDATE_SECTOR_INIT,
  ORDER_SECTOR_INIT, 
  FILTER_SECTOR_INIT,
  GET_SECTORS
} from './constants';
import { 
  selectSectorSuccess,
  getSectorsSuccess,
  getSectorsError, 
  deleteSectorSuccess,
  deleteSectorError,
  updateSectorSuccess,
  updateSectorError,
  orderSectorsSuccess,
  filterSectorsSuccess,
} from './actions';

function* selectSector(action) {
  const { value } = action;
  yield put(selectSectorSuccess(value));
  return;
}

function* getSectors() {
  const urlSectors = `${ PATH_API }${GET_SECTORS_API}`;

  const response = yield call(fetch, urlSectors, {
      method: 'GET',
  });

  if (response.status >= 200 && response.status < 300) {
    const successResponse = yield call([response, response.json]);
    yield put(getSectorsSuccess(successResponse));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(getSectorsError(errorResponse));
}

function* removeSector(action) {
  const { value } = action;
  const { id } = value ? value : {};

  const urlSectors = `${ PATH_API }${GET_SECTORS_API}/${id}`;

  const response = yield call(fetch, urlSectors, {
      method: 'DELETE',
  });

  if (response.status >= 200 && response.status < 300) {
    value.message = "Sector eliminado exitosamente"; 
    yield put(deleteSectorSuccess(value));
    return;
  }

  const errorResponse = yield call([response, response.json]);
  yield put(deleteSectorError(errorResponse));
}

function* updateSector(action) {
  const { value } = action;
  const { id, description, is_dangerous } = value ? value : {};
  value.message = "Sector creado exitosamente"; 

  let urlSectors = `${ PATH_API }${GET_SECTORS_API}`;

  const sectorToUpdate = {
    description,
    is_dangerous
  }

  const getResponse = yield call(fetch, `${urlSectors}/${id}`, {
    method: 'GET'
  });

  const callBody = {
    method: 'POST',
    body: JSON.stringify(sectorToUpdate)
  }

  if (getResponse.status >= 200 && getResponse.status < 300) {
    value.message = "Sector actualizado exitosamente"; 
    callBody.method = 'PUT';
    urlSectors = `${urlSectors}/${id}`
  }

  const response = yield call(fetch, urlSectors, {
      ...callBody
  });

  if (response.status >= 200 && response.status < 300) {
    yield put(updateSectorSuccess(value));
    return;
  }
  
  const errorResponse = yield call([response, response.json]);
  value.message = "Hubo un problema creando los cambios"; 
  yield put(updateSectorError(errorResponse));
}

function* orderSector(action) { 
  const { value: {column, items, sense} } = action;

  items.sort((a,b) => {
    if(a[column] < b[column]) {
      return sense;
    }
    if(a[column] > b[column]) {
      return -1*sense;
    }
    return 0;
  })

  const response = {
    sectors: items 
  }

  yield put(orderSectorsSuccess(response));
}

function* filterSector(action) { 
  const { value: {fieldValue, items} } = action;
  
  let filtered = items.filter(item => {
    let itemFiltered = false;

    Object.keys(item).map(key => {
      const keyToFilter = item[key] && typeof item[key] !== 'object' ? item[key].toString().toLowerCase() : ""; 
      itemFiltered = itemFiltered || keyToFilter.includes(fieldValue);
      return true;
    })

    return itemFiltered;
  });

  const response = {
    sectors: filtered
  }

  yield put(filterSectorsSuccess(response));
}

export function* watchSector() {
  yield takeLatest(GET_SECTORS, getSectors);
  yield takeLatest(SELECT_SECTOR_INIT, selectSector);
  yield takeLatest(DELETE_SECTOR_INIT, removeSector);
  yield takeLatest(UPDATE_SECTOR_INIT, updateSector);
  yield takeLatest(ORDER_SECTOR_INIT, orderSector);
  yield takeLatest(FILTER_SECTOR_INIT, filterSector);
}
