import React, { Component } from "react";
import "./style.css";
import { connect } from "react-redux";
import CustomForm from "../../components/customform/customform";
import CustomTable from "../../components/table/table";
import { getAddresses, getSelectedAddress, getDeleteAddress, getFormAddress } from "./selectors";
import { Spinner, Alert } from 'react-bootstrap';
import { 
  getAddressActions,
  getAddressButton,
  GET_ADDRESSES,
  SELECT_ADDRESS_INIT, 
  ADDRESS_FORMS, 
  DELETE_ADDRESS_INIT,
  UPDATE_FIELD_ATTRIBUTE,
  UPDATE_ADDRESS_INIT,
  CANCEL_ADDRESS_INIT
} from './constants'


class ComponentAddress extends Component {

  componentWillMount () {
    const { getAddresses } = this.props;
    getAddresses();
  }

  componentWillReceiveProps (nextProps) {
    const { getAddresses } = nextProps;

    if (nextProps.deleteAddress && this.props.deleteAddress !== nextProps.deleteAddress) {
      getAddresses();
    }
  }

  render() {
    const {
      addressHeaders, 
      addresses,
      selectAddress,
      selectedAddress,
      removeAddress,
      deleteAddress,
      updateAttributes,
      updatedAddress,
      updateAddress,
      cancelAddress
  } = this.props;

    return (
      <div className="App">
        { deleteAddress ?
          <Alert  variant="success">
            {deleteAddress.message}
          </Alert>
          : ''
        }

        {!selectedAddress ? 
          <div className='address-list_wrapper'>
          { addresses && !addresses.emprty ? 
            <CustomTable 
              onSelectRow={selectAddress} 
              className="addresses-table" 
              items={addresses} 
              headers={addressHeaders}
              buttons={getAddressButton(() => selectAddress({}))} 
            /> :
            <Spinner className="spinner-wrapper" animation="border" role="status">
              <span className="sr-only">Loading...</span>
            </Spinner>
          }
          
        </div> :  
        <div className='address-form_wrapper'>
          <CustomForm 
            formName="customAddress" 
            values={ selectedAddress } 
            items={ ADDRESS_FORMS } 
            updateAction = { updateAttributes }
            buttons={
              getAddressActions(
                () => updateAddress(updatedAddress), 
                () => cancelAddress(), 
                () => removeAddress(selectedAddress)
            )} 
            />
        </div>
        }
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    addressHeaders: state.addressHeaders,
    addresses: getAddresses(state),
    selectedAddress: getSelectedAddress(state),
    deleteAddress: getDeleteAddress(state),
    updatedAddress: getFormAddress(state)
  };
};

const mapDispachToProps = dispatch => {
  return { 
    getAddresses: () => dispatch({ type: GET_ADDRESSES, value: 1 }),
    updateAttributes: (value) => dispatch({ type: UPDATE_FIELD_ATTRIBUTE, value }),
    selectAddress: (address) => dispatch({ type: SELECT_ADDRESS_INIT, value: address }),
    removeAddress: (address) => dispatch({ type: DELETE_ADDRESS_INIT, value: address }),
    updateAddress: (address) => dispatch({ type: UPDATE_ADDRESS_INIT, value: address }),
    cancelAddress: () => dispatch({ type: CANCEL_ADDRESS_INIT}) 
  };
};

export default connect(
  mapStateToProps,
  mapDispachToProps
)(ComponentAddress);