import React, { Component } from 'react';
import Container from 'react-bootstrap/Container'
import "./sector.css";
import ComponentSector from './component';

import reducer from "./reducer";
import { createStore, applyMiddleware, combineReducers } from "redux";
import { composeWithDevTools } from 'redux-devtools-extension';
import createSagaMiddleware from "redux-saga";
import { watchSector } from "./saga";
import { Provider } from 'react-redux';
import { reducer as formReducer } from 'redux-form';
import CustomBreadcrumb from "../../components/breadcrumb/breadcrumb";
import { BREAD_PATH, } from './constants';

const rootReducer = combineReducers({
    form: formReducer,
    reducer
});

const formStore = createStore(rootReducer);

const sagaMiddleware = createSagaMiddleware();
const store = createStore(reducer, composeWithDevTools(applyMiddleware(sagaMiddleware)));
sagaMiddleware.run(watchSector);

export default class SectorsPage extends Component {
    render() {
        return (
            <Provider store={formStore}>
                <Provider store={store}>
                    <Container className='main-wrapper sectors' >
                        <CustomBreadcrumb route={BREAD_PATH} current="Sectores"/>
                        <h1> Lista de sectores </h1>
                        <ComponentSector />
                    </Container>
                </Provider>
            </Provider>
        );
    }
}

