import { takeLatest, put, call } from "redux-saga/effects";
import { GET_ZONES_API, PATH_API } from '../../Constants';
import { 
    GET_ZONE_INIT
} from './constants';
import { 
    getZoneSuccess,
    getZoneError
} from './actions';



function* getZonesSaga() {
    const urlSectors = `${ PATH_API }${GET_ZONES_API}`;

    const response = yield call(fetch, urlSectors, {
        method: 'GET',
    });

    if (response.status >= 200 && response.status < 300) {
        const successResponse = yield call([response, response.json]);
        yield put(getZoneSuccess(successResponse));
        return;
    }

    const errorResponse = yield call([response, response.json]);
    yield put(getZoneError(errorResponse));
}


export function* watchZones() {
    yield takeLatest(GET_ZONE_INIT, getZonesSaga);
}
